module.exports = "<h1 id=\"vue-js-third-days\">Vue.js Third days</h1>\n<h2 id=\"-\">单文件组件</h2>\n<ul>\n<li>把每一个组件单独的形成一个独立的文件，文件后缀为<code>.vue</code><ul>\n<li><code>xxx.vue</code>包含：跟当前组件相关的CSS,JS,HTML</li>\n<li>通过<code>webpack</code>打包工具把<code>xxx.vue</code>文件转化成<code>js</code>文件</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"webpack-\">webpack基本用法</h3>\n<ul>\n<li>安装<strong>webpack</strong> 打包工具(用于生产环境的打包)<ul>\n<li>是一个现代js应用程序的静态模块打包器<ul>\n<li>把浏览器不识别的文件打包成浏览器识别的文件</li>\n<li>对前端代码进行模块化开发，在webpack世界了，一切都是模块（可以遵循commonjs规范）<ul>\n<li>前端模块化开发的框架，（seajs requirejs babaljs webpack 等）</li>\n</ul>\n</li>\n<li>对项目进行一些工程化操作（自动合并文件，压缩文件，开启web服务器）</li>\n<li>使用vue开发项目时，vue已经提供了vue的脚手架（搭建了webpack关联vue的运行环境）</li>\n</ul>\n</li>\n<li>使用步骤<ol>\n<li>全局安装<code>npm install webpack webpack-cli -g</code></li>\n<li>创建一个项目，初始化环境<code>npm init</code>自动创建一个<strong>package.json</strong>文件</li>\n<li>局部安装<code>npm install webpack webpack-cli -D</code><ol>\n<li><code>-D  等同与  --save-dev</code></li>\n</ol>\n</li>\n<li>配置<code>webpack.config.js</code><ol>\n<li>入口函数<code>entry:__dirname+&#39;/app.js&#39;</code></li>\n<li>输出文件<code>output:{path:__dirname+&#39;/build&#39;,filename:&#39;bundle.js&#39;}</code></li>\n<li>加载器<code>loaders</code>把浏览器不认识的文件，转化浏览器认识的文件</li>\n<li>插件<code>plugins</code></li>\n<li>格式<code>mode:development</code>设置为开发者模式<code>mode:production</code>设置为生产模式</li>\n<li>映射源码<code>devtool:eval-source-map</code>调试错误很方便。</li>\n</ol>\n</li>\n<li>直接使用就好啦<ol>\n<li>可以通过<code>webpack -w</code>来实时监听文件的改变，就不需要每一次都重新打包一次了</li>\n</ol>\n</li>\n</ol>\n</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li><p>安装<strong>webpack-dev-server</strong>服务器(主要用于开发环境)</p>\n<ul>\n<li><p>依赖<code>webpack打包工具</code></p>\n</li>\n<li><p>全局安装<code>npm install webpack-dev-server -g</code></p>\n</li>\n<li><p>创建一个项目，初始化环境<code>npm init</code>自动创建一个<strong>package.json</strong>文件</p>\n</li>\n<li><p>局部安装<code>npm install webpack-dev-server -D</code></p>\n</li>\n<li><p>同样配置<code>webpack.config.js</code>文件</p>\n<ul>\n<li><blockquote>\n<p>devServer:{</p>\n<p>​    open:true自动打开浏览器</p>\n<p>​    port:3000监听端口号</p>\n<p>}</p>\n</blockquote>\n</li>\n<li><p>会自动把合并的文件生成到内存中，运行速度很快</p>\n</li>\n</ul>\n</li>\n<li><p>在当前环境下执行<code>webpack-dev-server</code></p>\n<ul>\n<li><p>会自动生成<code>bundle.js</code>文件在内存中。我们可以在<code>index.html</code>文件中引用</p>\n</li>\n<li><p>会自动寻找当前文件夹下的<code>index.html</code>并打开。否则在浏览器返回文件夹目录</p>\n</li>\n<li><p>自带热更新功能，修改了代码，自动重启服务器，刷新浏览器</p>\n</li>\n</ul>\n</li>\n<li><p>根据返回的端口号。打开浏览器</p>\n</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li><p><strong>webpack</strong> 的插件(html-webpack-plugin)</p>\n<ul>\n<li><p>下载插件npm install xxx -D</p>\n</li>\n<li><p>在<code>webpack.config.js</code>文件中引用这个模块<code>require(&#39;xxx&#39;)</code></p>\n</li>\n<li><p>在<code>webpack.config.js</code>文件中配置这个模块</p>\n<ul>\n<li><blockquote>\n<p>plugins:[</p>\n<p>​    new HtmlWebpackPlugin({</p>\n<p>​        template:__dirname+&#39;/src/index.html&#39;</p>\n<p>​    })</p>\n<p>]</p>\n<p>选择src文件夹下的index.html作为入口模板</p>\n<p>这个模块会把我们指定的html文件加载到缓存中，在缓存中加载缓存中的js文件，且不需要在HTML文件中指定路径</p>\n</blockquote>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li><p><strong>webpack</strong> 的<code>loaders</code></p>\n<ul>\n<li><p>下载安装依赖的loaders模块</p>\n<ul>\n<li>例如<code>cnpm install --save-dev style-loader css-loader</code></li>\n</ul>\n</li>\n<li><p>配置<code>webpack.config.js</code></p>\n<ul>\n<li><blockquote>\n<p>module:{</p>\n<p>​    rules:[</p>\n<p>​        {</p>\n<p>​            test:/\\.css$/,</p>\n<p>​            use:[</p>\n<p>​                 {</p>\n<p>​                    loader: &#39;url-loader&#39;,</p>\n<p>​                    options:{</p>\n<p>​                                name:&#39;[name].[ext]&#39;</p>\n<p>​                            }    </p>\n<p>​                }                        </p>\n<p>​                ]    </p>\n<p>​        },</p>\n<p>​    ]</p>\n<p>}</p>\n</blockquote>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"vue-js-\">Vue.js 的脚手架</h3>\n<ul>\n<li>全局安装vue命令行<code>npm install -g vue-cli</code></li>\n<li>局部安装<code>vue init webpack-simple my-project</code>安装学习版本的vue脚手架<ul>\n<li><code>my-project</code>可以自定义，使我们项目的名字</li>\n</ul>\n</li>\n<li>局部安装<code>vue init webpack my-project</code>安装实战版本的vue脚手架<ul>\n<li>安装过程中我们可以配置<ul>\n<li>项目名</li>\n<li>描述</li>\n<li>作者</li>\n<li>是否进行运行+编译 或者只是运行</li>\n<li>是否使用路由</li>\n<li>是否使用ESlint语法使用规则检测工具</li>\n<li>是否使用测试工具（默认摩卡）</li>\n<li>是否使用e2e测试工具</li>\n<li>是否使用npm或yarn作为操作工具</li>\n</ul>\n</li>\n<li><code>assets</code>文件夹下的文件会自动生成模块</li>\n<li><code>build</code>文件夹为webpack的配置文件夹</li>\n<li><code>config</code>为配置文件</li>\n<li><code>postcssrc</code>后处理css（可以自行了解一下）</li>\n<li><code>static放置</code>静态文件（不想被模块化的文件）</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"es6-\">ES6 的模块化开发</h3>\n<ul>\n<li><blockquote>\n<p>引入模块</p>\n<p>import variable form &#39;./xx.js&#39;</p>\n<p>import {名字一致,yes} form &#39;./xx.js&#39;</p>\n<p>引入css文件</p>\n<p>import &#39;xxx.css&#39;</p>\n<p>导出模块</p>\n<p>export default variable</p>\n<p>export {</p>\n<p>​    xx as 名字一致,</p>\n<p>​    mm as yes</p>\n<p>}</p>\n</blockquote>\n</li>\n</ul>\n<h3 id=\"-\">单文件组件</h3>\n<ul>\n<li><p>结构</p>\n<pre><code class=\"language-html\">&lt;tempalte&gt;&lt;/template&gt;\n\n&lt;script&gt;\n\n    export default {\n\n​    name:&#39;作为每个vue文件的标识ID&#39;\n\n }\n\n &lt;/script&gt;\n\n &lt;style scoped&gt;&lt;/style&gt;\n\n  如果想使用sass，需自己配置sass-loader/style-loader/css-loader/node-sass，然后&lt;style lang=&quot;scss&quot;&gt;</code></pre>\n</li>\n<li><p><code>static</code>文件夹</p>\n<ul>\n<li>该文件夹下的文件做静态资源，需要通过<code>href/src</code>引入‘/static/xxx.xx’</li>\n</ul>\n</li>\n<li><p><code>assets</code> 文件夹</p>\n<ul>\n<li>该文件夹下的文件做模块资源，需通过<code>import &#39;@/assets/xx/xxx.xx&#39;</code>方法引入</li>\n<li><code>@</code>在webpack中做别名指<code>src</code></li>\n</ul>\n</li>\n</ul>\n";


