<template>
	<div id="mycss">
		<header>
			<h1>CSS</h1>
		</header>
		<article class="w">
			<h2>初识CSS层叠样式表</h2>
			<h3>作者:<span>upeartaker</span></h3>
			<p>
				CSS，全称Cascading Style Sheets，层叠样式表
			</p>
		</article>
	</div>
</template>

<script>
	export default {
		name:'mycss'
	}
</script>

<style scoped>
	#mycss{
		width:100%;
		height:100%;
	}
	#mycss > header {
		width:100%;
		height:70px;
		background: white
	}
	#mycss > header > h1 {
		width:100%;
		height:50px;
		padding:10px;
		line-height: 30px;
		text-indent: 20px;
		background: moccasin;
	}
	#mycss > article > h2 {
		margin-bottom: 10px;
		color:black;
	}
	#mycss > article > p {
		margin-top:30px;
		text-indent: 2em;
	}
</style>


