<template>
  <div id="list">
    <h1>My demo</h1>
    <ul>
      <li v-for="(v,i) of list" :key="i" @click="handleRoute">{{v}}</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'mydemo',
    data() {
      return {
        list: ['贪吃蛇', '打飞机', '打砖块','简易画板','俄罗斯方块']
      }
    },
    methods:{
      handleRoute(evt){
        this.$router.push('/demo/'+evt.target.innerHTML)
      }
    }
  }
</script>

<style scoped lang="scss">
  $wd: 500px;
  #list {
    width: $wd;
    min-height: 350px;
    margin: 50px auto;
    border: 1px solid #ccc;
    box-shadow: 2px 2px 2px black;
    h1 {
      width: $wd;
      text-align: center;
      line-height: 70px;
      height: 70px;
      margin-bottom: 50px;
      border-bottom: antiquewhite 5px double;
    }
    ul {
      list-style: none;
      width: $wd;
      li {
        width: $wd;
        height: 30px;
        line-height: 30px;
        text-indent: 2em;
        font-size: 16px;
        color: #000;
        text-shadow: 2px 2px 2px #ccc;
        cursor: pointer;
        &:hover {
          background: #ccc;
          opacity: .7;
          transition: all .6s linear;
        }
      }
    }
  }
</style>




