<template>
  <div id="myvue">
    <header>
      <p>Vue.js 学习笔记</p>
    </header>
    <ul class="list">
      <li v-for="(item,index) of list" :key="index" @click="handleRouter(index)">{{item}}</li>
    </ul>
  </div>
</template>

<script>

  export default {
    name: "vuelist",
    data() {
      return {
        list:['vue.js for begin','vue.js for second','vue.js for third','vue.js for fourth','vue.js for fifth','vue.js for sixth','vue.js for seventh','vue_atual']
      }
    },
    mounted() {

    },
    methods:{
      handleRouter(index){
        this.$router.push('/myvue/'+ (index+1))
      }
    }
  }
</script>

<style scoped lang="scss">
  #myvue{
    width: 100%;
    height: 100%;
    header{
      height: 150px;
      width: 100%;
      background: url("../../assets/banner.jpg") center center no-repeat;
      background-size: cover;
      position: relative;
      left: 0;
      right: 0;
      margin: auto;
      p{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        width:200px;
        height: 30px;
        font-size: 24px;
        font-weight: bold;
        line-height: 30px;
        color: #0099ff;
        text-shadow: 2px 2px 2px #444444;
      }
    }
    .list{
      background: antiquewhite;
      height:calc(100% - 150px);
      padding:50px 100px 0px 100px;
      list-style: none;
      li{
        height:35px;
        font-size: 18px;
        text-indent: 20px;
        line-height: 35px;
        cursor: pointer;
        &:hover{
          background: bisque;
        }
      }
    }
  }
</style>




