module.exports = "<h1 id=\"vue-js-second-days\">Vue.js Second Days</h1>\n<h2 id=\"vue-js-event\">Vue.js Event</h2>\n<ul>\n<li><p>监听事件直接触发代码</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;div @click=&quot;message = &#39;hello&quot;&gt;\n    {{message}}\n&lt;/div&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p>方法事件处理器（写函数名）</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;div @click=&quot;handleClick&quot; id=&quot;app&quot;&gt;\n\n&lt;/div&gt;\n&lt;script&gt;\n    var vm = new Vue({\n        el:&quot;#app&quot;,\n        methods:{\n            handleClick(evt){\n                console.log(evt)//可以直接获取\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p>内联处理器方法（执行函数表达式）</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;div @click=&quot;handleClick($event)&quot; id=&quot;app&quot;&gt;\n\n&lt;/div&gt;\n&lt;script&gt;\n    var vm = new Vue({\n        el:&quot;#app&quot;,\n        methods:{\n            handleClick(evt){\n                console.log(evt)//传递参数才可获取。同时可以传递多个参数。\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p>为什么在HTML上监听事件</p>\n<ul>\n<li>你可能注意到这种事件监听的方式违背了关注点分离 (separation of concern) 这个长期以来的优良传统。但不必担心，因为所有的 Vue.js 事件处理方法和表达式都严格绑定在当前视图的 ViewModel 上，它不会导致任何维护上的困难。实际上，使用 <code>v-on</code> 有几个好处： <ul>\n<li>扫一眼 HTML 模板便能轻松定位在 JavaScript 代码里对应的方法。 </li>\n<li>因为你无须在 JavaScript 里手动绑定事件，你的 ViewModel 代码可以是非常纯粹的逻辑，和 DOM 完全解耦，更易于测试。 </li>\n<li>当一个 ViewModel 被销毁时，所有的事件处理器都会自动被删除。你无须担心如何清理它们。 </li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p>扩展es6字符串匹配方法</p>\n<ul>\n<li><pre><code class=\"language-javascript\">var str = &#39;hellow&#39;\nvar search = &#39;e&#39;\nstr.includes(search)//返回布尔值。找到就是true，没有就是false。\n\n//与es5的indexOf的区别。es5的方法返回的是索引</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-\">Vue的一些默认行为</h2>\n<ul>\n<li>vue中默认对一样的标签是进行复用的，这样可以减少DOM操作，提升性能<ul>\n<li>但是这种行为对于列表渲染的情况，性能是下降的。所以我们可以应用<code>:key</code>来解决这个问题</li>\n<li>不能用index作为<code>key</code>的值，因为索引的变化的</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js 生命周期</h2>\n<ul>\n<li><p>每个vue实例在创建之前，都会进行一系列的初始化过程。同时在这个过程中也会运行一些叫生命周期钩子的函数，给与用户在一些特定的时期，使用他们自己的函数。</p>\n</li>\n<li><p>如果想扩展自己的代码到vue中。</p>\n<ul>\n<li>不能直接改源码，我们目前没有那种能力</li>\n<li>利用生命周期钩子，在vue.js以外的代码区域进行修改</li>\n</ul>\n</li>\n<li><p>生命周期图示</p>\n<ul>\n<li>实例化vue</li>\n<li>初始化事件驱动和生命周期</li>\n<li>初始化数据，运行环境</li>\n<li>判断参数列表是否存在el属性/vm.$mount(el)</li>\n<li>判断参数列表是否存在template属性<ul>\n<li>如果存在用template模板替换生成DOM</li>\n<li>如果不存在使用HTML中写好的DOM</li>\n</ul>\n</li>\n<li>创建vm.$el属性，插值表达式替换页面中的变量</li>\n<li>完成安装，同时创建虚拟DOM，时时更新页面</li>\n<li>卸载</li>\n</ul>\n</li>\n<li></li>\n<li><p>说白了就是vue实例创建的时候，触发了很多事件函数(vue.js自己写好的)，并且这些事件函数中提供了许多接口（callback），来供用户自定义一些自己的代码，加入vue实例创建的并调用他们</p>\n<ul>\n<li><pre><code class=\"language-html\"> &lt;div id=&quot;app&quot;&gt;\n     {{message}}\n &lt;/div&gt;\n &lt;script&gt;\n     var vm = new Vue({\n         el:&#39;#app&#39;,\n         data:{\n             message:&#39;info&#39;\n         },\n         template:&#39;&lt;h2&gt;如果我存在，则会渲染我，不会渲染&lt;div id=&quot;app&quot;&gt;&lt;/&gt;&lt;/h2&gt;&#39;,\n         beforeCreate(){\n             console.log(this.$el)//undefined\n             console.log(this.$data)//undefined\n         },\n         create(){\n             console.log(this.$el)//undefined\n             console.log(this.$data)//{message:&#39;info&#39;}\n         },\n         beforeMount(){\n             console.log(this.$el)//存在，但没有用插值表达式替换数据\n             console.log(this.$data)//{message:&#39;info&#39;}\n         },\n         mounted(){\n             console.log(this.$el)//存在，插值表达式替换为真正数据\n             console.log(this.$data)//{message:&#39;info&#39;}\n         },\n         beforeUpdate(){\n             //更新数据之前调用（虚拟DOM发生了改变才会调用），但数据已经改变了\n             console.log(this.$data)//数据已经更新了\n             console.log(this.$el)//跟新的数据并没有渲染页面\n         },\n         update(){\n             //更新数据调用（虚拟DOM发生了改变才会调用）\n             console.log(this.$data)//数据已经更新了\n             console.log(this.$el)//视图重新渲染\n         },\n         boforeDestroy(){\n             //vm.$destroy 完全销毁一个实例。清理它与其它实例的连接，解绑它的全部指令及事件监听器。 销毁时被调用\n             console.log(this.$data)//数据已经更新了\n             console.log(this.$el)//视图重新渲染\n         },\n         destroy(){\n             //vm.$destroy \n             console.log(this.$data)//数据已经更新了\n             console.log(this.$el)//视图重新渲染\n         }\n     })\n &lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js 组件</h2>\n<ul>\n<li><p>将一个大型项目切割成更小的可控单元（类似模块化开发）</p>\n</li>\n<li><p>组件是可复用，可维护的</p>\n</li>\n<li><p>组件具有强大的封装性</p>\n</li>\n<li><p>大型项目中，组件与组件中是可以解耦操作的</p>\n</li>\n<li><p>在beforeMount和mounted中的过程挂载，也就是挂载el元素的时候，在控制的vue元素标签上挂载了组件</p>\n</li>\n<li><p>组件继承了vue的实例，同样具有methods,watch,computed,等方法,同样具有生命周期</p>\n<ul>\n<li>但是组件中的data必须是函数</li>\n<li>因为组件是需要复用的，所以组件中的数据都应该有一个独立的作用域，不能影响其他数据</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li><p>全局组件</p>\n<ul>\n<li><blockquote>\n<pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;my-header&gt;&lt;/my-header&gt;\n&lt;/div&gt;\n&lt;script&gt;\n    Vue.component(&#39;my-header&#39;,{\n        template:&#39;&lt;h2&gt;我是一个全局组件{{message}}&lt;/h2&gt;&#39;,\n        data(){\n            return {\n                message:&#39;haha&#39;\n            }\n        }\n    })\n\n    var vm = new Vue({\n        el:&#39;#app&#39;\n    })\n&lt;/script&gt;</code></pre>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p>局部组件</p>\n<ul>\n<li><blockquote>\n<pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;my-header&gt;&lt;/my-header&gt;\n&lt;/div&gt;\n&lt;script&gt;\n\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        components:{\n             &#39;my-header&#39;:{\n                  template:&#39;&lt;h2&gt;我是一个局部组件{{message}}&lt;/h2&gt;&#39;,\n                 data(){\n                        return {\n                            message:&#39;haha&#39;\n                        }\n        }\n            },\n             &#39;my-list&#39;:{\n                template:&#39;&lt;h2&gt;我是一个局部组件&lt;/h2&gt;&#39;\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p>组件嵌套</p>\n<ul>\n<li><blockquote>\n<pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;my-header&gt;&lt;/my-header&gt;\n&lt;/div&gt;\n&lt;script&gt;\n\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        components:{\n             &#39;my-header&#39;:{\n                  template:&#39;&lt;h2&gt;我是一个局部组件{{message}},&lt;my-title&gt;&lt;my-title&gt;&lt;/h2&gt;&#39;,\n                 data(){\n                        return {\n                            message:&#39;haha&#39;\n                        }\n                        },\n                 components:{\n                     &#39;my-title&#39;:{\n                         template:&#39;&lt;h3&gt;我是一个嵌套的组件&lt;h3&gt;&#39;\n                     }\n                 }\n            },\n             &#39;my-list&#39;:{\n                template:&#39;&lt;h2&gt;我是一个局部组件&lt;/h2&gt;&#39;\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</blockquote>\n</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"vue-js-\">Vue.js 组件的模板方式</h3>\n<ul>\n<li><p>组件中的template必须有一个唯一的根元素</p>\n</li>\n<li><p>js字符串不能折行</p>\n<ul>\n<li>用  <strong>+</strong>  运算符进行连接</li>\n<li>用  <strong>\\</strong>  运算符进行连接（后面不能有space） </li>\n<li>用es6的模板字符串</li>\n</ul>\n</li>\n<li><p>利用<code>&lt;template&gt;</code>标签</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;template id=&quot;tem&quot;&gt;\n    &lt;div&gt;\n        这里是vue的模板标签\n    &lt;/div&gt;\n&lt;/template&gt;\n\n&lt;script&gt;\n    var vm = new Vue({\n        components:{\n            &#39;my-header&#39;:{\n                template:&#39;#item&#39;\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p>利用<code>&lt;javascript type=&quot;text/x-template&quot;&gt;</code>标签</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;javascript id=&quot;tem&quot; type=&quot;text/x-template&quot;&gt;\n    &lt;div&gt;\n        这里是vue的模板标签\n    &lt;/div&gt;\n&lt;/javascript&gt;\n\n&lt;script&gt;\n    var vm = new Vue({\n        components:{\n            &#39;my-header&#39;:{\n                template:&#39;#tem&#39;\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n";


