module.exports = "<h1 id=\"vue-js-five-days\">Vue.js five days</h1>\n<h2 id=\"vue-js-router\">Vue.js Router</h2>\n<ul>\n<li><p>后端路由</p>\n<ul>\n<li>多个页面跳转。</li>\n</ul>\n</li>\n<li><p>前端路由</p>\n<ul>\n<li>做spa页面跳转的，只有一个页面</li>\n<li>通过hash + onhashchange原理</li>\n<li>或通过history + onpop + pushstate 原理</li>\n</ul>\n</li>\n<li><p>使用vue的路由</p>\n<ul>\n<li><p>安装vue-router模块，并创建单独的router.js文件</p>\n<ul>\n<li><code>cnpm i -S vue-router</code>    </li>\n</ul>\n</li>\n<li><pre><code class=\"language-js\">import Vue from &#39;vue&#39;\nimport VueRouter from &#39;vue-router&#39;\n//初始化路由\nVue.use(VueRouter);\n//定义路由\nconst Foo = {template:&#39;&lt;div&gt;foo&lt;/div&gt;&#39;}//foo组件\nconst routes = [\n    {path:&#39;/foo&#39;,component:Foo},\n    {path:&#39;/*&#39;,component:&#39;/404&#39;}//页面重定向(状态码302)\n]\n//注册路由\nconst router = new VueRouter({\n    method:&#39;history&#39;//选取模式，历史模式和哈希模式\n    routes\n})\nexport default router</code></pre>\n</li>\n<li><pre><code class=\"language-javascript\">//在main.js 中引入\nimport router form &#39;./router/router.js&#39;\nnew Vue({\n    router\n})</code></pre>\n</li>\n</ul>\n</li>\n<li><p>路由搭建完毕，路由插座</p>\n<ul>\n<li>展示组件的区域</li>\n<li>通过<code>&lt;router-view&gt;&lt;/router-view&gt;</code>嵌入页面</li>\n</ul>\n</li>\n<li><p>路由的句柄（点击跳转路由）</p>\n<ul>\n<li>通过<code>&lt;router-link to=&quot;/foo&quot;&gt;&lt;/router-link&gt;</code>来实现<ul>\n<li>这个标签默认在页面上渲染成<code>&lt;a&gt;</code>标签</li>\n<li>可以通过tag属性指定渲染标签<code>tag=&quot;button&quot;</code></li>\n<li>还可以通过<code>.router-link-active:{}</code>来指定被渲染的<code>&lt;router-link&gt;</code>标签的样式</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p>路由的嵌套（二级路由+）</p>\n<ul>\n<li><p>一级路由配置中配置二级路由</p>\n</li>\n<li><pre><code class=\"language-javascript\">const routes = [\n    {\n        path:&#39;/foo&#39;,\n        component:Foo,//在一级路由的template标签中插入二级路由的&lt;router-view&gt;标签\n        children:[\n            {\n                path:&#39;imtwo&#39;,//注意这里没有/ ，否则会被认为是绝对路径，不写是相对路径\n                component:xxx\n            }\n        ],\n        redirect:&#39;/foo/imtwo&#39;\n        //设置进入foo路由后，默认跳转到/foo/intwo这个二级路由\n    },\n\n]</code></pre>\n</li>\n</ul>\n</li>\n<li><p>动态路由</p>\n<ul>\n<li><p>根据类似的一批url，可以跳转到同一个组件</p>\n</li>\n<li><pre><code class=\"language-javascript\">//通过在配置路由时，设置路径来实现动态路由\n{\n    path:&#39;/foo/:variable&#39;,component:foo\n}\n//variable是变量我们可以在生命周期中或其他时期通过 this.$route.params.variable来获取url上面的值    </code></pre>\n</li>\n</ul>\n</li>\n<li><p>编程式路由</p>\n<ul>\n<li>在js中进行路由跳转，叫做编程式路由</li>\n<li>vue的实现<code>$router.push(&#39;/details&#39;)</code></li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vuex-\">Vuex 状态管理</h2>\n<ul>\n<li><p>vuex是第三方模块</p>\n<ul>\n<li>借鉴了<em>flux</em>架构思想</li>\n</ul>\n</li>\n<li><p>Vuex状态管理模块</p>\n<ul>\n<li>状态===数据</li>\n<li>做非父子组件通信，适合做中大型项目</li>\n<li>不适合父子通信</li>\n</ul>\n</li>\n<li><p>图示</p>\n<ul>\n<li>vuex是单项是数据流动</li>\n<li>组件向改变state中的数据<ul>\n<li>dispatch-&gt;actions-&gt;commit-&gt;mutations-&gt;mutate-&gt;state异步操作</li>\n<li>commit-&gt;mutations-&gt;mutate-&gt;state同步操作<ul>\n<li>为什么不能在mutations做异步操作，会影响devtools这个工具，跟踪不到数据的变化</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p><img src=\"" + require("./vuex.png") + "\" alt=\"\"></p>\n</li>\n<li><p>具体操作</p>\n<ol>\n<li><p>局部安装 <code>cnpm i -S vuex</code></p>\n<ol>\n<li>在<code>src</code>文件夹下创建store文件夹，在这个文件夹下创建store.js</li>\n</ol>\n</li>\n<li><p>了解Devtools调试工具</p>\n<ol>\n<li>配置是否允许 <a href=\"https://github.com/vuejs/vue-devtools\">vue-devtools</a> 检查代码。开发版本默认为 <code>true</code>，生产版本默认为 <code>false</code>。生产版本设为 <code>true</code> 可以启用检查。 </li>\n<li>在Chrome商店下载，或在百度上下载</li>\n<li>通过chrome的扩展程序来扩展这个调试工具</li>\n<li>这个工具同步监听了mutations中发生的变化</li>\n</ol>\n</li>\n<li><p>vuex的核心内容</p>\n<ol>\n<li>State ： 存储共享的状态（数据）</li>\n<li>Getter：vuex的计算属性</li>\n<li>Mutations：来实现状态的同步操作</li>\n<li>Action：来实现状态的异步操作</li>\n<li>Module：多状态管理模块划分</li>\n</ol>\n</li>\n<li><p><code>store.js</code>文件内容</p>\n<ol>\n<li><pre><code class=\"language-javascript\">import Vue from &#39;vue&#39;\nimport Vuex from &#39;vuex&#39;\n//初始化vuex\nVue.use(Vuex);\n//定义vuex配置\nconst store = new Vuex.Store({\n    //定义公共状态\n    state:{\n        count:0\n    },\n    actions：{\n        //用于异步操作，方法名也都是大写的\n            CHANGE_COUNT_ACT(context,payload){\n            setTimeout(()=&gt;{\n                //context 执行上下文，相当于this，这里是$store\n                context.commit(&#39;CHANG_COUNT&#39;,payload)\n            },1000)\n        }\n    },\n    mutations:{\n        //这里的方法名都是大写的\n        //用于同步操作，不能进行异步操作，否则devtools检测不到\n        CHANGE_COUNT(state,payload){\n            state.count++\n            console.log(payload)//可以接收数据\n        }\n    },\n    getters:{\n        //计算属性：用法和computed一样，可以当做属性传递\n        computed(state){\n            return state\n        }\n    }\n\n})\n//提供对外接口\nexport default store</code></pre>\n</li>\n<li><p><code>main.js</code>中操作</p>\n<pre><code class=\"language-javascript\">import store from &#39;./store/store.js&#39;\nnew Vue({\n    store\n})</code></pre>\n</li>\n<li><p>这样我们就能通过<code>$store.state.count</code>来获取<code>state</code>中定义的数据了</p>\n</li>\n<li><p>如何修改<code>state</code>中的数据</p>\n<ol>\n<li>通过在<code>mutations</code>中定义方法,可以通过<code>$store.commit(&#39;CHANGE_COUNT&#39;,123)</code>来触发<ol>\n<li>第二个参数为想传递的数据</li>\n</ol>\n</li>\n<li>通过在<code>action</code>中定义的方法，可以通过<code>$store.dispatch(&#39;CHANGE_COUNT_ACT&#39;,payload)</code>来触发<ol>\n<li>我们在这里进行异步操作。</li>\n</ol>\n</li>\n</ol>\n</li>\n</ol>\n</li>\n<li><p><code>vuex</code>的简写操作</p>\n<ul>\n<li><p><code>state</code>  =&gt; <code>mapState</code></p>\n<ul>\n<li><pre><code class=\"language-javascript\">import {mapState} from &#39;vuex&#39;;\nmethods:{\n    ...mapState([&#39;xxx&#39;,&#39;xxxx&#39;])\n}</code></pre>\n</li>\n</ul>\n</li>\n<li><p><code>getters</code>  =&gt; <code>mapGetters</code></p>\n</li>\n<li><p><code>mutations</code>  =&gt; <code>mapMotations</code></p>\n</li>\n<li><p><code>actions</code>  =&gt; <code>mapActions</code></p>\n</li>\n<li><p>利用es6的展开运算符<code>...</code></p>\n<ul>\n<li><p>```javascritp\nvar a = [1,2,3,4];\nvar b = a;\nb.push(5)\n//影响了a</p>\n<p>//利用展开运算符进行堆的复制\nvar b = [...a]</p>\n<p>/////////////////////////////\nvar obj = {</p>\n<pre><code>username:&#39;xiaoming&#39;</code></pre><p>}</p>\n<p>var cloneObj = {...obj}</p>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ol>\n</li>\n</ul>\n<pre><code>     ```</code></pre><ol start=\"6\">\n<li><p><code>modules</code>中可以引入其他的状态管理，进行模块化划分</p>\n<pre><code class=\"language-js\">export default {\n    state:{},\n    getters:{},\n    actions:{},\n    mutations:{}\n}</code></pre>\n<pre><code class=\"language-js\">import moduleA from &#39;xxx.js&#39;\n\nconst store = new Vuex.Store({\n    modules:{\n        moduleA:moduleA\n    }\n})\n//注意 模块是作为整体输出的\n//在你的state属性必须用mapState，且mapState中的值作为对象的形式存在，且对应一个函数</code></pre>\n</li>\n</ol>\n";


