<template>
  <div id="two">
    <div v-html="md" v-highlight></div>
  </div>
</template>

<script>
  import '@/assets/markdown.css';
  import md2 from './note/vue.js for second day .md'
  export default {
    name: "vuetwo",
    data(){
      return {
        md:md2
      }
    }
  }
</script>

<style scoped>
  #two{
    width: 100%;
    height: 100%;
    padding:0 100px 0 100px;
  }
</style>




