<template>
  <div id="tetris">
    <h1>Trtris</h1>
    <canvas width="400" height="400" ref="cans"></canvas>
  </div>
</template>

<script>
    export default {
        name: "Tetris",
        data(){
          return {
            gridding:'',
            drawMould:null,
            typeMould:1,
          }
        },
        methods:{

        },
        mountd(){
          var ctx = this.$refs.cans.getContext('2d');
          //绘画模具
          this.drawMould = function () {
            if(this.typeMould === 1){
              ctx.save();
              ctx.fillRect(100,0,10,10);
              ctx.restore();
            }
          }
        }
    }
</script>

<style scoped lang="scss">
  #tetris{
    width:100%;
    height: 100%;
    position: absolute;
    h1{
      width: 100%;
      height: 30px;
      text-align: center;
      line-height: 30px;
      margin-top: 20px;
    }
    canvas{
      background: lightblue;
      display: block;
      margin: 0 auto;
    }
  }
</style>



