<template>
  <div id="box">
    <canvas width="500" height="500" ref="cans" @mousedown="drawing"></canvas>
    <div id="control">
      <ul>
        <li v-for="(item,index) of list" :key="index" :contenteditable="index == 6 ? true : false "
            @click="changeColor($event,index)" @blur="changeState($event,index)">{{item}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "draw",
    data() {
      return {
        list: ['black', 'yellow', 'red', 'green', 'blue', 'clear', '0'],
        color: 'black',
        count: 4
      }
    },
    methods: {
      drawing(evt) {
        var ctx = this.$refs.cans.getContext('2d');
        var x = evt.offsetX;
        var y = evt.offsetY;
        // ctx.clearRect(0, 0, this.$refs.cans.width, this.$refs.cans.height);
        ctx.beginPath();
        ctx.moveTo(x, y);
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.count;
        window.onmousemove = function (evt) {
          var x = evt.offsetX;
          var y = evt.offsetY;
          ctx.lineTo(x, y);
          ctx.stroke();
          return false
        }
        window.onmouseup = function (evt) {
          ctx.closePath();
          window.onmousemove = window.onmouseup = null
        }
      },
      changeColor(evt,index) {
        if(index < this.list.length -2) {
          this.color = evt.target.innerHTML
        }
        if(index === this.list.length -2){
          this.color = 'white'
        }
      },
      changeState(evt,index){
        if(index === this.list.length -1 ){
          this.count = evt.target.innerHTML
        }
      }
    },
    mounted() {
      document.body.style.background = 'black'
    }
  }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  body {
    background: black
  }

  canvas {
    background: white;
    transform: translate(-50%, -50%);
    position: absolute;
    left: 50%;
    top: 50%;
  }

  #control {
    text-align: center;
    font-size: 12px;
    color: white;
    transform: translate(0, -50%);
    position: absolute;
    right: 10%;
    top: 50%;
  }

  ul {
    list-style: none
  }

  li {
    width: 50px;
    height: 30px;
    line-height: 30px;
    background: grey;
    border-bottom: 1px solid black;
    cursor: pointer;
  }
</style>



