<template>
  <div id="worldofwar">
    <div class="all">
      <div @click="myhtml">HTML</div>
      <div @click="mycss">CSS</div>
      <div @click="myjs">Javascript</div>
      <div>TODO</div>
      <div>TODO</div>
      <div>TODO</div>
      <div @click="mydemo">Demo</div>
      <div @click="myblogs">Blogs</div>
      <div>TODO</div>
      <div @click="profile">profile</div>
      <div @click="myvue">Vue.js</div>
      <div>React</div>
      <div>Angular</div>
    </div>
    <p class="about">
	  	Click on the fan to see my profile
	</p>
  </div>
</template>

<script>
  export default {
    name: 'worldofwar',
    methods:{
    	myhtml(){
    		this.$router.push('/html')
    	},
      mycss(){
        this.$router.push('/css')
      },
      myjs(){
        this.$router.push('/javascript')
      },
      profile(){
        this.$router.push('/profile')
      },
      mydemo(){
        this.$router.push('/demo')
      },
      myblogs(){
        this.$router.push('/blogs')
      },
      myvue(){
    	  this.$router.push('/myvue')
      }
    }
  }
</script>

<style scoped>

  #worldofwar {
    width: 100%;
    height: 100%;
    background: url("../assets/homebg.jpg") center center;
    overflow: hidden;
  }

  #worldofwar .all {
    width: 500px;
    height: 300px;
    margin: 50px auto;
    border-bottom: 2px solid #000;
    position: relative;
  }

  #worldofwar .all div {
    width: 50px;
    height: 250px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    background: #0CC;
    box-shadow: 0px 0px 2px #666;
    transition: all 1s;
    transform-origin: bottom;
    letter-spacing: 1px;
    white-space: pre-wrap;
    word-break: break-all;
    text-align: center;
  }
  #worldofwar .all:hover div{
  	cursor:pointer;
  }
  #worldofwar .all:hover div:nth-child(1) {
    transform: rotate(-90deg);
    background: #f00;
  }

  #worldofwar .all:hover div:nth-child(2) {
    transform: rotate(-75deg);
    background: #f60;
  }

  #worldofwar .all:hover div:nth-child(3) {
    transform: rotate(-60deg);
    background: #ff0;
  }

  #worldofwar .all:hover div:nth-child(4) {
    transform: rotate(-45deg);
    background: #0f0;
  }

  #worldofwar .all:hover div:nth-child(5) {
    transform: rotate(-30deg);
    background: #0ff;
  }

  #worldofwar .all:hover div:nth-child(6) {
    transform: rotate(-15deg);
    background: #00f;
  }

  #worldofwar .all:hover div:nth-child(7) {
    transform: rotate(0deg);
    background: #f0f;
  }

  #worldofwar .all:hover div:nth-child(8) {
    transform: rotate(15deg);
    background: #00f;
  }

  #worldofwar .all:hover div:nth-child(9) {
    transform: rotate(30deg);
    background: #0ff;
  }

  #worldofwar .all:hover div:nth-child(10) {
    transform: rotate(45deg);
    background: #0f0;
  }

  #worldofwar .all:hover div:nth-child(11) {
    transform: rotate(60deg);
    background: #ff0;
  }

  #worldofwar .all:hover div:nth-child(12) {
    transform: rotate(75deg);
    background: #f60;
  }

  #worldofwar .all:hover div:nth-child(13) {
    transform: rotate(90deg);
    background: #f00;
  }
  #worldofwar .about {
  	width:100%;
  	height:30px;
  	text-align: center;
  	font-weight: bold;
  	line-height: 30px;
  	font-size: 24px
  }
</style>



