module.exports = "<h1 id=\"vue-js-about-project\">Vue.js About Project</h1>\n<h2 id=\"swiper-\">swiper 框架</h2>\n<ul>\n<li><p>移动端轮播图</p>\n</li>\n<li><p>移动端滑动菜单</p>\n</li>\n<li><p><code>swiper.com.cn</code></p>\n</li>\n<li><p>必须等待DOM全部加载完毕后才生效</p>\n<ul>\n<li>用vm.$nextTick().then(callback)</li>\n</ul>\n</li>\n<li><pre><code class=\"language-html\">&lt;!DOCTYPE html&gt;\n&lt;html lang=&quot;en&quot;&gt;\n&lt;head&gt;\n    &lt;meta charset=&quot;UTF-8&quot;&gt;\n    &lt;title&gt;Document&lt;/title&gt;\n    &lt;link rel=&quot;stylesheet&quot; href=&quot;swiper-4.3.5.min.css&quot;&gt;\n    &lt;style&gt;\n    #banner{ width:400px; height:200px; border:1px black solid; margin:20px auto;}\n    #banner .swiper-slide{ text-align: center; line-height: 200px; background:red;}\n\n    #list{ width:400px; height:30px; border:1px black solid; margin:20px auto;}\n    #list .swiper-slide{ width:50px; text-align: center; line-height: 30px; }\n    &lt;/style&gt;\n    &lt;script src=&quot;swiper-4.3.5.min.js&quot;&gt;&lt;/script&gt;\n&lt;/head&gt;\n&lt;body&gt;\n    &lt;div id=&quot;banner&quot; class=&quot;swiper-container&quot;&gt;\n        &lt;div class=&quot;swiper-wrapper&quot;&gt;\n            &lt;div class=&quot;swiper-slide&quot; style=&quot;background:red&quot;&gt;Slide 1&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot; style=&quot;background:hotpink&quot;&gt;Slide 2&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot; style=&quot;background:blue&quot;&gt;Slide 3&lt;/div&gt;\n        &lt;/div&gt;\n        &lt;!-- 如果需要分页器 --&gt;\n        &lt;!-- &lt;div class=&quot;swiper-pagination&quot;&gt;&lt;/div&gt; --&gt;\n        &lt;div class=&quot;swiper-pagination swiper-pagination-fraction&quot;&gt;\n            &lt;span class=&quot;swiper-pagination-current&quot;&gt;1&lt;/span&gt; / &lt;span class=&quot;swiper-pagination-total&quot;&gt;3&lt;/span&gt;\n        &lt;/div&gt;\n        &lt;!-- 如果需要导航按钮 --&gt;\n        &lt;div class=&quot;swiper-button-prev&quot;&gt;&lt;/div&gt;\n        &lt;div class=&quot;swiper-button-next&quot;&gt;&lt;/div&gt;    \n        &lt;!-- 如果需要滚动条 --&gt;\n           &lt;div class=&quot;swiper-scrollbar&quot;&gt;&lt;/div&gt;\n    &lt;/div&gt;\n\n    &lt;div id=&quot;list&quot; class=&quot;swiper-container&quot;&gt;\n        &lt;div class=&quot;swiper-wrapper&quot;&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;首页&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;新闻&lt;/div&gt;\n            &lt;div class=&quot;swiper-slide&quot;&gt;视频&lt;/div&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n    &lt;script&gt;\n    /*var mySwiper = new Swiper(&#39;#banner&#39;, {});*/\n\n    /*var mySwiper = new Swiper(&#39;#banner&#39;, {\n        //loop : true,\n        direction: &#39;vertical&#39;,\n        //autoplay : true,\n        autoplay : {\n            delay : 2000\n        },\n        speed : 3000\n    });*/\n\n    var mySwiper = new Swiper(&#39;#banner&#39;, {\n        effect: &#39;fade&#39;,\n        pagination: {\n          el: &#39;.swiper-pagination&#39;,\n          type : &#39;fraction&#39;\n        },\n        navigation: {\n          nextEl: &#39;.swiper-button-next&#39;,\n          prevEl: &#39;.swiper-button-prev&#39;\n        },\n        scrollbar: {\n          el: &#39;.swiper-scrollbar&#39;\n        }\n    });\n\n    var mySwiper = new Swiper(&#39;#list&#39;,{\n        freeMode : true,   // 随意滑动距离\n        slidesPerView: &#39;auto&#39;,  //根据每一项自身的宽度来适应容器\n        freeModeSticky: true //滑动到每一项的端点\n    }); \n\n    &lt;/script&gt;\n&lt;/body&gt;\n&lt;/html&gt;</code></pre>\n</li>\n</ul>\n<h2 id=\"fetch\">Fetch</h2>\n<ul>\n<li><p>ES6的新提供的一种ajax操作，基于<code>promise</code>来设计的</p>\n</li>\n<li><p>后端接收到数据的格式</p>\n<ul>\n<li><code>GET</code>：字符串</li>\n<li><code>POST</code>：字符串，JSON，二进制</li>\n</ul>\n</li>\n<li><pre><code class=\"language-javascript\">//GET\nfetch(&#39;data.php?message=hellow&#39;).then((res)=&gt;{\n    console.log(res);\n    return res.json()//先转化json格式，这里的res是一个对象\n}).then((data)=&gt;{\n    console.log(data)//这里的data是服务器返回的数据\n})\n\n//POST（默认上传JSON）\nfetch(&#39;data.php,{\n      method:&#39;POST&#39;,\n      headers:{\n          &#39;Content-type&#39;:&#39;application/x-www-form-urlencoded&#39;//默认是jsonp的格式，这里给转化成字符串\n      }\n      body:&#39;这是我上传的数据&#39;\n      }).then((res)=&gt;{\n    console.log(res);\n    return res.json()//先转化json格式，这里的res是一个对象\n}).then((data)=&gt;{\n    console.log(data)//这里的data是服务器返回的数据，会把后端格式自动转化为字符串\n})</code></pre>\n</li>\n</ul>\n<h2 id=\"axios\">Axios</h2>\n<ul>\n<li><p>下载axios.js文件</p>\n</li>\n<li><pre><code class=\"language-html\">&lt;script src=&quot;./axios.js&quot;&gt;&lt;/script&gt;\n&lt;script&gt;\n    //get\n    axios.get(&#39;data.php&#39;,{\n        params:{\n            message:&#39;hellow&#39;//这里是向后端发送的数据\n        }\n    })then((res)=&gt;{\n        console.log(res)//同样是一个对象，但这个对象中直接由一个属性就是我们要请求的数据\n        console.log(res.data)//会把后端格式自动解析为字符串\n    })\n    //post 默认传给后端是json格式\n    var options = {\n        method:&#39;POST&#39;,\n        url:&#39;data.php&#39;,\n        headers:{\n            &#39;Content-Type&#39;:&#39;application-x-www-form-urlencoded&#39;//自定义请求头部类型\n        }\n        data:&#39;message=hellow&#39;//不支持对象\n    }\n    axios(options).then((res)=&gt;{\n        console.log(res)//同样是一个对象，但这个对象中直接由一个属性就是我们要请求的数据\n        console.log(res.data)//会把后端格式自动解析为字符串\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n<h2 id=\"cros-\">CROS 跨域资源共享</h2>\n<ul>\n<li><p>针对浏览器安全策略（同源策略）</p>\n</li>\n<li><p>一下这几种类型设计到跨域</p>\n<ul>\n<li>域名不同</li>\n<li>协议不同</li>\n<li>端口不同</li>\n<li>ip和域名</li>\n</ul>\n</li>\n<li><p>解决方式</p>\n<ul>\n<li>jsonp：利用<code>script</code>标签来实现</li>\n<li>cors：服务器设置白名单</li>\n<li>反向代理</li>\n<li>domain</li>\n<li>window.name</li>\n<li>iframe</li>\n<li>postMessage</li>\n<li>正向代理</li>\n<li>websocket</li>\n<li>flash</li>\n</ul>\n</li>\n<li><p>CORS跨域资源共享</p>\n<ul>\n<li><pre><code class=\"language-php\">//后端:设置白名单\n&lt;?php\n    header(&#39;Access-Control-Allow-Origin:http://xxxxx&#39;)\n    ?&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"-\">反向代理</h2>\n<ul>\n<li><p>前端通过访问后端，由后端反向代理访问其它网站后端，来获取数据</p>\n</li>\n<li><pre><code class=\"language-javascript\">vue通过webpack-dev-server来实现的服务器。\n所以我们可以在webpack-dev-server的配置文件中的proxyTable属性来设置反向代理\nproxyTable:{\n    &#39;/v4/api/*&#39;:{\n        target:&#39;https://m.maizuo.com/&#39;,\n        host:&#39;m.maizuo.com&#39;,\n        changeOrigin:true    \n    }\n}\n///v4/api/为我们想代理的或后端向访问的文件夹。</code></pre>\n</li>\n</ul>\n<h2 id=\"-\">移动端事件与框架</h2>\n<ul>\n<li><p>移动端事件</p>\n<ul>\n<li><code>ontouchstart</code> ==&gt;onmousedown</li>\n<li><code>ontouchmove</code>==&gt; onmousemove</li>\n<li><code>ontouchend</code>==&gt;onmouseup</li>\n</ul>\n</li>\n<li><p>移动端的事件对象</p>\n<ul>\n<li>移动端的事件对象跟pc端事件对象并不是完全相同</li>\n<li>他有touch对象是一个伪数组，var touch = evt.changeTouches   这里存放着clientX,clientY等</li>\n</ul>\n</li>\n<li><p>zepto库</p>\n<ul>\n<li><p>用法类似jQuery</p>\n</li>\n<li><p>这个库提供了许多移动端的事件</p>\n<ul>\n<li>滑屏</li>\n<li>触屏</li>\n<li>长按</li>\n<li>双指</li>\n<li>等等</li>\n</ul>\n</li>\n<li><pre><code class=\"language-javascript\">//利用zepto监听滑屏事件(任何方向滑动都可以触发)（swipeLeft左滑事件）\n$(&#39;#box&#39;).on(&#39;swipe&#39;,()=&gt;{\n\n})\n//监听触屏事件，类似原生的touchstart。但原生的touchstart无法区分滑动和点击，只要手指按下就会触发。tap不会。同样还有singleTap单点事件，doubleTap双击事件，langTap长按事件\n$(&#39;#box&#39;).on(&#39;tap&#39;,()=&gt;{\n\n})\n//监听双指操作事件。\n//pinch双指放大缩小都会触发\n//pinchIn双指放大\n//pinchOut双指缩小\n$(&#39;#box&#39;).on(&#39;pinch&#39;,()=&gt;{\n\n})</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"-\">逻辑运算符的本质</h2>\n<ul>\n<li>五大运算符之一</li>\n<li>&amp;&amp;运算符<ul>\n<li>本质：前面的值为真就返回后面的值，前面的值为假就返回前面的值</li>\n</ul>\n</li>\n<li>|| 运算符<ul>\n<li>本质：前面的值为真就返回前面的值，前面的值为假就返回后面的值</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"git\">GIT</h2>\n<ul>\n<li>git工具<ul>\n<li>版本控制工具，是一种记录一个或者若干个文件内容变化，以便将来在特定版本修订情况的系统。</li>\n<li>在项目中进行多人写作开发</li>\n<li>下载地址<ul>\n<li><a href=\"https://git-scm.com/\">https://git-scm.com/</a></li>\n<li><a href=\"https://desktop.github.com\">https://desktop.github.com</a></li>\n</ul>\n</li>\n<li>github网站<ul>\n<li>程序员社交平台。开源项目库，远程仓库，个人博客等。</li>\n<li><a href=\"https://githun.com\">https://githun.com</a></li>\n<li>中国版：码云</li>\n</ul>\n</li>\n<li>集成式管理工具<ul>\n<li>所有项目版本都在中央服务器上</li>\n<li>必须要网络</li>\n<li>cvs，svn</li>\n</ul>\n</li>\n<li>分布式管理工具<ul>\n<li>每一台电脑中都有版本</li>\n<li>git</li>\n</ul>\n</li>\n<li>下载安装</li>\n</ul>\n</li>\n<li>使用bash<ul>\n<li>git自带的命令行操作窗口<ul>\n<li>支持Linux命令</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>git的配置<ul>\n<li>git config --list查看配置菜单</li>\n<li>git config --global user.name &quot;xxx&quot;配置贡献者姓名</li>\n<li>git config --global user.email &quot;xxx&quot;配置贡献者邮箱</li>\n<li>git config --global --unset user.name 删除配置</li>\n</ul>\n</li>\n<li>git的三个区<ul>\n<li>工作区（Gitstudy文件夹）<ul>\n<li>在这个文件夹下右键打开bash命令行</li>\n<li>输入命令git init初始化git环境</li>\n</ul>\n</li>\n<li>暂存区（.git文件夹）</li>\n<li>版本区（.git文件夹）<ul>\n<li>主分支master</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>git的常用命令<ul>\n<li>git init初始化环境</li>\n<li>git status    查看git的状态<ul>\n<li>工作区文件与缓存区，版本区有区别，则文件显示红色</li>\n<li>工作区文件与缓存区一样，版本区有区别，则显示绿色</li>\n<li>工作区文件与缓存区，版本区一致，则显示白色</li>\n</ul>\n</li>\n<li>git add xxx   将xxx文件添加到缓存区<ul>\n<li>git add . 将所有文件添加到暂存区</li>\n</ul>\n</li>\n<li>git commit -m  将暂存区的所有文件提交到版本区（-m “嘻嘻嘻嘻嘻嘻嘻” 为这次提交文件的注释）<ul>\n<li>git commit -a -m 一次性将已经被管理过得文件提交到版本区（-a是add的简写）</li>\n</ul>\n</li>\n<li>git log 输出所有日志</li>\n<li>git diff 对比文件差异<ul>\n<li>git diff 工作区与缓存区的对比</li>\n<li>git diff --cached（--staged） 缓存区与版本区的对比</li>\n<li>git diff master 工作区与版本区的对比（master为分支的名字）</li>\n</ul>\n</li>\n<li>git reset HEAD xxx 撤销版本区最新版本xxx文件到缓存区  也叫检出<ul>\n<li>git checkout --xxx 从缓存区或版本区撤销最新版本到工作区</li>\n<li>git checkout id --xxx 从缓存区或版本区将指定id版本文件撤销到工作区</li>\n</ul>\n</li>\n<li>git rm xxx 将文件删除（git commit -m 提交后，这个文件在这个版本就彻底不存在了）<ul>\n<li>git rm -f xxx 强制将文件从工作区删除</li>\n<li>git rm --cached xxx 将文件从缓存区删除</li>\n</ul>\n</li>\n<li>git reset --hard id 将工作区恢复到指定版本<ul>\n<li>与撤销的区别，撤销版本，版本区不会发生变化</li>\n<li>恢复版本，版本区指定版本后的版本都不存在了</li>\n<li>git reflog 输出最近操作的日志<ul>\n<li>我们可以通过git reflog 打印出恢复版本后丢失的版本号，在重新找回丢失版本</li>\n</ul>\n</li>\n<li>git reset --hard HEAD^ 使指针指向上一个版本，恢复到上一个版本</li>\n</ul>\n</li>\n<li>HEAD指针<ul>\n<li>git中有一个head指针，指向了最新版本，我们其实通过head指针，来获取版本</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"github-\">GitHub 远程仓库</h2>\n<ul>\n<li><p>SSH安全协议</p>\n<ul>\n<li><p>指定电脑允许在GitHub上传文件</p>\n</li>\n<li><p>先检查电脑administrator下是否有.ssh文件</p>\n</li>\n<li><p>没有在git命令行中输入ssh-keygen -t rsa -C &quot;<a href=\"mailto:youremail@example.com\">youremail@example.com</a>&quot;（你自己的邮箱）</p>\n</li>\n<li><p>打开生成的.ssh文件</p>\n</li>\n<li><p>在GitHub中配置</p>\n</li>\n<li><p>git remote 查看远程连接</p>\n</li>\n<li><p>git remote -v 查看具体远程连接路径</p>\n</li>\n<li><pre><code>git remote add origin https://github.com/upeartaker/mobile_maizuo.git添加一个远程连接路径，并起名为origin，指定路径为https://github.com/upeartaker/mobile_maizuo.git</code></pre></li>\n<li><p>git push orgin master 选择要上传远程连接名字，以及要上传的分支</p>\n</li>\n</ul>\n</li>\n<li><p>多人协作</p>\n<ul>\n<li>接受邀请</li>\n<li>git clone + adress 克隆一个git仓库</li>\n<li>解决多人写作会造成同一代码多人修改产生冲突的问题<ul>\n<li>git pull branch<ul>\n<li>下载文件，并进行对比</li>\n</ul>\n</li>\n<li>git fetch<ul>\n<li>下载文件，不进行对比</li>\n<li>通过git diff master origin / master</li>\n<li>通过git merge origin/master进行对比</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p>操作分支</p>\n<ul>\n<li>git branch 查看当前分支</li>\n<li>git branch xxx 新建一个分支</li>\n<li>git branch -d xxx 删除一个分支</li>\n<li>git checkout xxx选择分支</li>\n<li>git checkout -b 创建并切换一个新分支</li>\n<li>git merge xxx 合并指定分支（注意分支不会消失，需要自己删除）</li>\n</ul>\n</li>\n<li><p>操作里程碑</p>\n<ul>\n<li>git tag v1.0.0 添加一个里程碑</li>\n<li>git push origin v1.0.0 上传里程碑</li>\n</ul>\n</li>\n</ul>\n";


