module.exports = "<h1 id=\"vue-js-for-seven\">Vue.js for seven</h1>\n<ul>\n<li>给vue.js添加所创建的网站添加一个可以通过ip来访问的方法<ul>\n<li>在package.json中的脚本中<code>dev</code>  添加  --host 0.0.0.0</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js项目上线</h2>\n<ul>\n<li><code>npm run build</code>创建一个dist文件</li>\n<li>后端解决反向代理接口问题</li>\n<li>将前端生成的文件分发给后端的文件夹<ul>\n<li>static放入public中</li>\n<li>index.html放到views文件夹中</li>\n<li>index.html 更名为index.ejs</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js自定义指令</h2>\n<ul>\n<li><pre><code class=\"language-html\">Vue.directive()全局定义指令\ndirectives:{}局部定义指令</code></pre>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;div v-color=&quot;xxx&quot;&gt;\n        123\n    &lt;/div&gt;\n&lt;/div&gt;\n\n&lt;script&gt;\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        directives:{\n            //定义一个v-color指令\n            &#39;color&#39;:{\n                 //指令里面有指令特有的生命周期\n                bind(){\n                    //绑定指令生命周期\n                },\n                inserted(el,binding){\n                    //指令插入完毕\n                    //每个指令接受两个参数，第一个参数为绑定指令的元素，第二个参数为绑定指令内的执行环境参数xxx\n                    el.style.background = binding.value\n                },\n                update(){\n                    //指令更新\n                    el.style.background = binding.value\n                },\n                componentUpdate(){\n                    //指令更新结束\n                },\n                unbind(){\n                    //指令解绑\n                }\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n<li><pre><code class=\"language-javascript\">//当所有环境内容一样的时候，可以直接简写为\n&#39;color&#39;:function(el,binding){\n    el.style.background = binding.value\n}</code></pre>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js 过滤器</h2>\n<ul>\n<li><p>vue.js自带了很多过滤器，我们可以查文档，一般都是一些简单的函数</p>\n</li>\n<li><p>同样我们可以自定义过滤器</p>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    {{message | firstUpper | throudthed(&#39;xxx&#39;)}}\n    &lt;!----可以写多个过滤器----&gt;\n&lt;/div&gt;\n\n&lt;script&gt;\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        data:{\n            message:&#39;hello&#39;,\n            number:1231414123123\n        },\n        filters:{\n            firstUpper(val){\n                //在定义的数据后面写|过滤器函数名称 就会把数据传输给过滤器的参数中，我们就可以操作这个参数了\n                return val.slice(0,1).toUpperCase()+val.slice(1)\n            },\n            throudthed(val,rag){\n                //第二个参数为标签中传递过来的变量\n                return Number(val).toLocaleString()//转化千分符\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js 的性能优化</h2>\n<ul>\n<li>key属性，可以添加到列表循环中提高性能</li>\n<li>keep-alive，可以利用这个标签对组件进行缓存处理，提高性能</li>\n<li>v-show，使用频繁的显示隐藏。提高性能</li>\n<li>异步组件可以改善中大型项目的加载</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js异步组件</h2>\n<ul>\n<li><p>用到这个组件的时候，才会加载。用不到的时候，不会加载</p>\n</li>\n<li><p>适合中大型网站</p>\n</li>\n<li><p>不适合小型网站（产生额外的http请求）</p>\n</li>\n<li><pre><code class=\"language-js\">//我们可以在配置路由时，或引入组件是这么写\ncompoent:()=&gt;import &#39;@components/xxxx&#39;\n//这样我们不就不会预加载所有组件，而是在我们请求这个组件是，才会引入这个组件</code></pre>\n</li>\n</ul>\n<h2 id=\"postman\">POSTMAN</h2>\n<ul>\n<li>用来测试接口数据</li>\n<li>可以测试get/post等请求，且不需要写代码</li>\n</ul>\n<h2 id=\"-vue-js-ui-\">基于Vue.js的UI框架</h2>\n<ul>\n<li><p>mint_ui（饿了么团队制作的移动端UI框架）</p>\n<ul>\n<li><p>基于vue框架使用</p>\n<ul>\n<li><p>可以通过npm下载【npm install -S mint_ui】</p>\n</li>\n<li><p>在main.js中引用直接,也可以像vuex一样，专门创建一个文件来配置。但这个模块配置比较简单，可以直接在main.js中配置</p>\n</li>\n<li><p>```js\nimport Vue from &#39;vue&#39;;\nimport mint from &#39;mint_ui&#39;;\nimport &#39;mint_ui/lib/style.css&#39;\nVue.use(mint);</p>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<pre><code>  ```\n\n- 自带许多组件，自带样式。只要根据框架提供的语法，就可以直接使用\n\n  - 分为ui组件和js组件</code></pre><ul>\n<li><p>组件之间的引用</p>\n<ul>\n<li>父子组件之间的引用，子组件必须写在父组件的<code>template</code>属性中，否则不会渲染</li>\n<li>兄弟组件的引用，可以直接在自定义标签中引用，但注意外部组件的<code>template</code>属性中应该有插槽标签<slot></solt></li>\n</ul>\n</li>\n</ul>\n<h2 id=\"-vue-js-\">深入 Vue.js 了解响应式原理</h2>\n<pre><code class=\"language-html\">&lt;!DOCTYPE html&gt;\n&lt;html lang=&quot;en&quot;&gt;\n&lt;head&gt;\n    &lt;meta charset=&quot;UTF-8&quot;&gt;\n    &lt;title&gt;Document&lt;/title&gt;\n&lt;/head&gt;\n&lt;body&gt;\n    &lt;script&gt;\n    //正常创建一个对象\n    var obj = {\n        message : &#39;hello&#39;,\n        username : &#39;xiaoming&#39;\n    };\n\n    /*console.log( obj.message );\n\n    obj.message = &#39;hi&#39;;\n\n    delete obj.message;\n\n    console.log( obj.message );*/\n\n    /*for(var attr in obj){   //枚举操作\n        console.log(attr);\n    }*/\n\n    //Object.defineProperty() : 改变对象中属性的默认值\n\n    /*console.log(Object.getOwnPropertyDescriptor(obj, &#39;message&#39;));//列举一个对象属性的底层属性\n\n    { \n        value: &quot;hello&quot;, \n        writable: true, 是否可以读写\n        enumerable: true, 是否可以枚举\n        configurable: true    是否可以删除\n    }*/\n\n    //从js底层创建或修改一个对象属性  \n    Object.defineProperty(obj , &#39;age&#39; , {\n        value : 20,\n        //这里什么都不写，所有默认为false\n    }); \n\n    //console.log( obj );\n\n    //console.log(Object.getOwnPropertyDescriptor(obj, &#39;age&#39;));\n\n    obj.age = 30;\n\n    console.log( obj.age );\n\n\n\n    &lt;/script&gt;\n&lt;/body&gt;\n&lt;/html&gt;</code></pre>\n";


