import Vue from 'vue'
import Router from 'vue-router'
import worldofwar from '@/components/worldofwar'
import myhtml from '@/components/myhtml'
import mycss from '@/components/mycss'
import myjs from '@/components/myjs'
import profile from '@/components/profile'
import mydemo from '@/components/mydemo'
import myblogs from '@/components/myblogs'
import snake from '@/components/mydemo/snake'
import brick from '@/components/mydemo/brick'
import plane from '@/components/mydemo/plane'
import list from '@/components/mydemo/list'
import draw from '@/components/mydemo/draw'
import Tetris from '@/components/mydemo/Tetris'
import myvue from '@/components/myvue'
import vuelist from '@/components/vue_note/vuelist'
import vueone from '@/components/vue_note/vueone'
import vuetwo from '@/components/vue_note/vuetwo'
import vuethree from '@/components/vue_note/vuethree'
import vuefour from '@/components/vue_note/vuefour'
import vuefive from '@/components/vue_note/vuefive'
import vuesix from '@/components/vue_note/vuesix'
import vueseven from '@/components/vue_note/vueseven'
import vueeight from '@/components/vue_note/vueeight'

Vue.use(Router)

export default new Router({
  routes: [
    {
    	path:'/',
    	component:worldofwar
    },

    {
    	path:'/html',
    	component:myhtml
    },

    {
        path:'/css',
        component:mycss
    },

    {
        path:'/javascript',
        component:myjs
    },

    {
        path:'/profile',
        component:profile
    },

    {
        path:'/demo',
        component:mydemo,
        children:[
          {
            path:'贪吃蛇',
            component:snake
          },
          {
            path:'打砖块',
            component:brick
          },
          {
            path:'打飞机',
            component:plane
          },
          {
            path:'list',
            component:list
          },
          {
            path:'简易画板',
            component:draw
          },
          {
            path:'俄罗斯方块',
            component:Tetris
          }
        ],
        redirect:'/demo/list'
    },

    {
        path:'/blogs',
        component:myblogs
    },

    {
      path:'/myvue',
      component:myvue,
      children:[
        {
          path:'list',
          component:vuelist
        },
        {
          path:'1',
          component:vueone
        },
        {
          path:'2',
          component:vuetwo
        },
        {
          path:'3',
          component:vuethree
        },
        {
          path:'4',
          component:vuefour
        },
        {
          path:'5',
          component:vuefive
        },
        {
          path:'6',
          component:vuesix
        },
        {
          path:'7',
          component:vueseven
        },
        {
          path:'8',
          component:vueeight
        },
      ],
      redirect:'/myvue/list'
    },

    {
    	path:'/*',
    	component:worldofwar
    }
  ]
})



