module.exports = "<h1 id=\"vue-js-to-begin\">vue.js To Begin</h1>\n<h2 id=\"-vue-js\">初识vue.js</h2>\n<ol>\n<li><p>构建数据驱动的web应用开发框架，属于前端框架</p>\n</li>\n<li><p>声明式渲染，应对前后端分离的大趋势</p>\n<ol>\n<li><pre><code class=\"language-javascript\">var message = &#39;hello world&#39;;\n&lt;div&gt;message&lt;/div&gt;  \n页面显示：&lt;div&gt;hello world&lt;/div&gt;</code></pre>\n</li>\n<li><p>声明式渲染，同样是响应式。</p>\n</li>\n<li><p>node项目用的是混合式开发。把前端的代码，和ejs结合在一起。</p>\n</li>\n</ol>\n</li>\n<li><p>渐进式框架，适应各种业务需求以及场景</p>\n<ol>\n<li>适合新手，同样适合有一定经验的开发人员。</li>\n<li>小型，中型，大型项目都可以采用VUE框架开发。</li>\n<li>渐进式框架：非常容易上手，渐进增强。</li>\n</ol>\n</li>\n<li><p>适合移动端开发，SPA页面，即单页开发</p>\n<ol>\n<li>移动端网路不是很稳定。期望项目制作为单页面，尽量避免跳转页面。</li>\n<li>SPA页面，即单页面开发，适用于vue.js框架。</li>\n</ol>\n</li>\n<li><p>快速交付，结合第三方ui框架</p>\n<ol>\n<li>一个项目，用jQuery开发和用vue开发的对比，vue会节省30%移上的代码。</li>\n<li>接口很多，可以结合很多UI框架。</li>\n<li>jQueryUI框架：easyUI，minUI等。</li>\n<li>vue相关UI框架：elementUI,minUI等。</li>\n<li>查阅vue官网自行寻找。</li>\n</ol>\n</li>\n<li><p>侧重于数据。不需要操作DOM，框架自动操作。</p>\n</li>\n</ol>\n<h2 id=\"-\">设计模式</h2>\n<ul>\n<li>每一种语言都是设计模式。<ul>\n<li>前人总结的一种开发经验。</li>\n<li>23种设计模式（book）<ul>\n<li>例如观察者模式</li>\n<li>工厂模式</li>\n<li>适配器模式等等</li>\n</ul>\n</li>\n<li>架构的设计模式（不属于23种设计模式）<ul>\n<li>MVC<ul>\n<li>model数据 views视图 controller控制器<ul>\n<li>利用控制器把数据和视图链接在一起</li>\n<li>view代码多 model代码相对多 control代码少</li>\n<li>node项目</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>MVP<ul>\n<li>model数据 views视图 presenter提出者<ul>\n<li>model和view代码相对多。presenter代码最多</li>\n<li>jQuery项目</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>MVVM（vue.js）<ul>\n<li>vm数据视图层<ul>\n<li>每层代码都很少</li>\n<li>vue.js 帮我们实现了vm层</li>\n<li>vue,js帮我们做了所有的DOM操作</li>\n<li>利用了双向数据绑定（observer）</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"-\">开发环境搭建</h2>\n<ul>\n<li><p>vue.js v-2.*.*</p>\n</li>\n<li><p>声明式渲染</p>\n</li>\n<li><p>组件系统</p>\n</li>\n<li><p>路由机制</p>\n</li>\n<li><p>状态管理</p>\n</li>\n<li><p>安装方式</p>\n<ul>\n<li>直接引用vue.js</li>\n<li>利用npm安装</li>\n<li>利用cmd安装等</li>\n</ul>\n</li>\n<li><p>版本区别</p>\n<ul>\n<li><p>开发版本</p>\n<ul>\n<li><blockquote>\n<p>项目开发阶段使用，没有代码压缩，并且有错误提</p>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p>生产版本</p>\n<ul>\n<li><blockquote>\n<p>正式版本，代码压缩，没有错误提示</p>\n</blockquote>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">VUE.JS的基本语法</h2>\n<h3 id=\"-\">基本操作</h3>\n<pre><code class=\"language-html\">&lt;script src=&quot;vue.js&quot;&gt;&lt;/script&gt;\n&lt;div id=&quot;app&quot;&gt;\n    {{message}},{{test}}\n&lt;/div&gt;\n&lt;div v-text=&quot;message&quot;&gt;\n\n&lt;/div&gt;\n&lt;script&gt;\n    //1.vue主要我们完成vm层开发\n    //2.el属性表示vue要控制的区域\n    //3.data数据对象，定义声明式的数据\n    //4.通过插值表达式{{js的执行环境}}，渲染页面。表达式中可以进行基本的js运算，可以写运算符，一些语法，但不能写语句。\n    //5.在vue中，基于面向对象的开发new Vue（）方式，在一个类中写了一个{}，这个叫配置参数。根据参数的不同，可以创建不同形态的对象。\n    //6.vm对象下，$ 和 _一般表示这些属性都是vue自带的属性和方法。\n    //7.vm.$data中的数据，挂载到了vm对象下，方便用户操作和内部使用。\n    //8.v-* :vue自带的指令（vue帮我实现了一些功能）\n    var vm = new VUE({\n        el:&#39;#app&#39;,\n        data:{\n            message:&#39;hellow vue&#39;,\n            test:&#39;我是一个测试文本&#39;\n        }\n    })\n&lt;/script&gt;</code></pre>\n<h3 id=\"vue-\">VUE常用指令</h3>\n<ul>\n<li><p><code>v-*</code>  vue.js自带13种指令</p>\n</li>\n<li><p><code>v-text</code>  渲染数据。v-text=&quot;变量/&#39;字符串&#39;&quot;。直接渲染在标签内，作为标签的属性。不会解析html语法</p>\n</li>\n<li><p><code>v-html</code>  渲染数据。会自动解析html语法</p>\n<ul>\n<li>不建议在data数据中写html标签，这样解析html标签，不安全，可能会被攻击(XSS)，(CSRF)</li>\n</ul>\n</li>\n<li><p><code>v-once</code>  一次性插值。给标签添加v-once属性。关闭标签的响应式。可以在特定情况下提升性能</p>\n</li>\n<li><p><code>v-bind</code>  操作HTML属性。是vue中使用最为频繁的指令之一；可简写为:</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;div v-bind:title=&quot;variable&quot;&gt;\n    用vue来管理title属性，这样title中的值就是一个变量（在data中赋值variable:&#39;这是用来显示的标题&#39;）\n&lt;/div&gt;</code></pre>\n</li>\n<li><pre><code class=\"language-html\">&lt;div :title=&quot;true? &#39;box&#39; : &#39;box2&#39;&quot;&gt;\n    简写，省略了v-bind，只保留了:\n&lt;/div&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p><code>v-on</code>  给html添加事件。同样是vue使用最为频繁的指令之一；可简写为@</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;div v-on:click=&quot;show&quot; id=&quot;event&quot;&gt;\n\n&lt;/div&gt;\n&lt;div @click.stop=&quot;show&quot; id=&quot;event&quot;&gt;\n\n&lt;/div&gt;\n&lt;script&gt;\n    //vm对象中有一个methods用来写事件的方法\n    //methods属性中的方法同样会被自动挂载到vm对象中。也就是vm.methods.show === vm.show\n    var vm = new Vue({\n        el:&#39;event&#39;,\n        data:{\n\n        },\n        methods:{\n            show(){\n                console.log(123)\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n<li><p><code>.stop</code> - 调用 <code>event.stopPropagation()</code>。</p>\n</li>\n<li><p><code>.prevent</code> - 调用 <code>event.preventDefault()</code>。</p>\n</li>\n<li><p><code>.capture</code> - 添加事件侦听器时使用 capture 模式。</p>\n<ul>\n<li>触发捕获阶段。不触发冒泡阶段</li>\n</ul>\n</li>\n<li><p><code>.self</code> - 只当事件是从侦听器绑定的元素本身触发时才触发回调。</p>\n<ul>\n<li>停止事件流动。事件只在本身触发</li>\n</ul>\n</li>\n<li><p><code>.{keyCode | keyAlias}</code> - 只当事件是从特定键触发时才触发回调。</p>\n<ul>\n<li><code>&lt;div @keydown.13&gt;&lt;/div&gt;</code></li>\n</ul>\n</li>\n<li><p><code>.native</code> - 监听组件根元素的原生事件。</p>\n<ul>\n<li>组件标签上的事件都是自定义事件，需要用$emit触发</li>\n<li>可以通过加<code>.native</code>  ,使组件的自定义事件变成原生事件</li>\n</ul>\n</li>\n<li><p><code>.once</code> - 只触发一次回调。</p>\n</li>\n<li><p><code>.left</code> - (2.2.0) 只当点击鼠标左键时触发。</p>\n<ul>\n<li><blockquote>\n<p>还有 .enter .ctrl .alt .meta .space等等</p>\n<p>还可以在.enter.exact达到精确匹配的目的，（同时按下其他键无效）</p>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p><code>.right</code> - (2.2.0) 只当点击鼠标右键时触发。</p>\n</li>\n<li><p><code>.middle</code> - (2.2.0) 只当点击鼠标中键时触发。</p>\n</li>\n<li><p><code>.passive</code> - (2.3.0) 以 <code>{ passive: true }</code> 模式添加侦听器</p>\n<ul>\n<li>提升移动端性能</li>\n<li>跟<code>.prevent</code> 完全相反的操作</li>\n<li>提前告诉浏览器我不去阻止默认行为，用来提升性能</li>\n</ul>\n</li>\n<li><p>对象语法（2.4.0+）</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;button v-on=&quot;{ mousedown: doThis, mouseup: doThat }&quot;&gt;&lt;/button&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p><code>v-show</code>  条件渲染</p>\n<ul>\n<li>与<code>v-if</code> 的区别是<code>v-show</code>是操作css属性display</li>\n<li><code>&lt;div v-show=&quot;true&quot;&gt;&lt;/div&gt;</code></li>\n<li>根据表达式的真假值，切换元素的display css属性</li>\n<li>当条件发生变化时该指令触发过度效果</li>\n<li>适用于频繁的显示隐藏</li>\n</ul>\n</li>\n<li><p><code>v-if</code>  条件渲染</p>\n<ul>\n<li>对html进行显示隐藏，对DOM进行原生的<em>appendchild</em>和<em>removechild</em></li>\n<li><code>&lt;div v-if=&quot;Math.rondam() &gt; 0.5&quot;&gt;&lt;/div&gt;</code></li>\n<li>根据表达式的值的真假条件渲染元素。在切换时，元素及它的数据绑定/组件被销毁并重建。</li>\n<li>但如果切换时，两个标签完全一样，vue会对便签进行复用（如<code>&lt;input&gt;</code>）</li>\n<li>如果元素时<code>&lt;template&gt;</code> ,将提出它的内容作为条件块。<code>&lt;template&gt;</code>标签不会出现在DOM节点上。是vue自定义的标签，用来充当临时容器。</li>\n<li>当条件发生变化时，该指令触发过渡效果</li>\n<li>适用于不频繁的显示隐藏</li>\n</ul>\n</li>\n<li><p><code>v-else-if</code>  条件渲染</p>\n<ul>\n<li><code>&lt;div v-else-if=&quot;Math.rondam() = 0.5&quot;&gt;&lt;/div&gt;</code></li>\n<li>前一个兄弟元素必须有<code>v-if</code>或<code>v-else-if</code>。</li>\n</ul>\n</li>\n<li><p><code>v-else</code>   条件渲染</p>\n<ul>\n<li><code>&lt;div v-else&gt;&lt;/div&gt;</code></li>\n<li>前一个兄弟元素必须有<code>v-if</code>或<code>v-else-if</code>。</li>\n</ul>\n</li>\n<li><p><code>v-for</code>   列表渲染</p>\n<ul>\n<li><p>基于源数据多次渲染元素或模板块。此指令之值，必须使用特定语法 <code>alias in expression</code> ，为当前遍历的元素提供别名： </p>\n</li>\n<li><pre><code class=\"language-html\">&lt;div v-for=&quot;item in items&quot;&gt;\n  {{ item }}\n&lt;/div&gt;\n&lt;script&gt;\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        data:{\n           items:[111,222,333] \n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n<li><p>另外也可以为数组索引指定别名 (或者用于对象的键)： </p>\n</li>\n<li><pre><code class=\"language-html\">&lt;div v-for=&quot;(item, index) in items&quot;&gt;{{item}},{{index}}&lt;/div&gt;&lt;!--返回数组的值，和索引-&gt;\n&lt;div v-for=&quot;(val, key) in object&quot;&gt;&lt;/div&gt;&lt;!--返回对象的值，和属性-&gt;\n&lt;div v-for=&quot;(val, key, index) in object&quot;&gt;&lt;/div&gt;&lt;!--返回对象的值，和属性，索引&gt;</code></pre>\n</li>\n<li><p>默认行为试着不改变整体，而是替换元素。迫使其重新排序的元素，你需要提供一个 <code>key</code> 的特殊属性： </p>\n</li>\n<li><pre><code class=\"language-html\">&lt;div v-for=&quot;item in items&quot; :key=&quot;item.id&quot;&gt;\n  {{ item.text }}\n&lt;/div&gt;\n//key属性的作用区分不用的节点。否则vue会对节点进行复用</code></pre>\n</li>\n<li><p><em>item in items</em>可以用 <em>item of items</em> 代替 因为es6 有一个for of循环。趋势所使</p>\n<ul>\n<li><pre><code class=\"language-javascript\">var arr = [1,2,3,4,5]\nfor(let v of arr){\n    console.log(v)\n}\n//for of 方法不支持对象的遍历，我们可以通过下面的方法解决这个问题\nObject.keys(obj);//把一个对象的属性转化成数组\nObject.values(obj);//把一个对象的值转化成数组\nObject.entries(obj);//把一个对象的属性和值转化成二维数组</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p><code>v-model</code>  表单控件绑定</p>\n<ul>\n<li>限制<ul>\n<li><code>&lt;input&gt;</code></li>\n<li><code>&lt;select&gt;</code></li>\n<li><code>&lt;textarea&gt;</code></li>\n<li><code>&lt;components&gt;</code></li>\n</ul>\n</li>\n<li><code>&lt;input v-model=&quot;variable&quot; /&gt;</code></li>\n<li>修饰符<ul>\n<li><code>.lazy</code> - 取代 <code>input</code> 监听 <code>change</code> 事件<ul>\n<li>当光标移开输入框才能触发响应式</li>\n</ul>\n</li>\n<li><code>.number</code> - 输入字符串转为数字<ul>\n<li>输入框默认会把输入内容转成字符串</li>\n<li>表单返回值返回数字</li>\n</ul>\n</li>\n<li><code>.trim</code> - 输入首尾空格过滤<ul>\n<li>页面上看不到效果</li>\n<li>会在表单的返回值上表示</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>在表单控件或者组件上创建双向绑定。 在通过watch等方法实现实时更新</li>\n<li>如果<code>v-model=&quot;true&quot;</code>会默认选中单选框或多选框</li>\n</ul>\n</li>\n<li><p><code>v-pre</code>  跳过这个元素和它的子元素的编译过程。可以用来显示原始 Mustache <a href=\"%E6%98%AF%E4%B8%80%E7%A7%8D%E6%A8%A1%E6%9D%BF%E5%BC%95%E6%93%8E\">^mustache</a>标签。跳过大量没有指令的节点会加快编译。</p>\n<ul>\n<li><code>&lt;div v-pre&gt;&lt;/div&gt;</code> </li>\n</ul>\n</li>\n<li><p><code>v-cloak</code>  隐藏渲染</p>\n<ul>\n<li>这个指令保持在元素上直到关联实例结束编译。和 CSS 规则如 <code>[v-cloak] { display: none }</code> 一起用时，这个指令可以隐藏未编译的 Mustache 标签直到实例准备完毕。 </li>\n<li><code>&lt;div v-cloak&gt;&lt;/div&gt;</code></li>\n</ul>\n</li>\n</ul>\n<h3 id=\"computed-\">Computed ：计算属性（对象）</h3>\n<ul>\n<li>computed对象里都是方法，但用的时候当属性来用。不需要调用</li>\n<li>同时我们也可以使用methods里面定义的函数，但这个函数必须调用，否则会在页面上直接显示代码</li>\n</ul>\n<pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    {{messageFn}}\n    {{messageMe()}}\n&lt;/div&gt;\n&lt;script&gt;\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        data:{\n\n        },\n        methods:{\n            messageMe(){\n                return &#39;hello world&#39;\n            }  \n        },\n        computed:{\n            messageFn(){\n               return &#39;hello world&#39;\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n<ul>\n<li><p>优点</p>\n<ul>\n<li><p>跟插值表达式中写语法相比，要更简洁，方便。</p>\n</li>\n<li><p>跟methods相比，计算属性是基于他们的依赖进行缓存的，计算属性只有在相关依赖进行改变时，才会发生改变。所以要比methods跟快一些</p>\n</li>\n<li><p>所以我们不能直接更改computed定义的方法。如上面的例子vm.messageFn = xxx会报错。当时我们可以通过将computed定义的方法改成对象的写法来更改</p>\n<ul>\n<li><pre><code class=\"language-javascript\">computed:{\n    messageFn:{\n        set(newVal){\n            this.message = newVal\n        },\n        get(){\n            return this.message       \n        }\n    }\n}</code></pre>\n</li>\n</ul>\n</li>\n<li><p>计算属性跟watch相比，更加简单易用</p>\n</li>\n<li><p>计算属性不适合异步数据操作</p>\n</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"watch-\">Watch 监听数据变化的属性（对象）</h3>\n<ul>\n<li>一般适用于异步的数据操作</li>\n</ul>\n<pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    {{messageFn}}\n    {{messageMe()}}\n&lt;/div&gt;\n&lt;script&gt;\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        data:{\n            message:&#39;hello world&#39;\n        },\n        methods:{\n            messageMe(){\n                return this.message\n            }  \n        },\n        computed:{\n            messageFn(){\n               return this.message\n            }\n        },\n        watch:{\n            message(newVal,oldVal){\n                //当message属性发生变化时，触发\n                console.log(&#39;新值&#39;,newVal);\n                console.log(&#39;旧值&#39;,oldVal)\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n<h3 id=\"class-style-\">Class 与 Style的绑定</h3>\n<ul>\n<li><p><em>class</em> 的对象语法</p>\n<ul>\n<li><blockquote>\n<p><code>&lt;div :class=&quot;{{box:true , white: false}}&quot;&gt;&lt;/div&gt;</code></p>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p><em>class</em> 的数组的语法</p>\n<ul>\n<li><blockquote>\n<div :class=\"[box,box2,box3]\"></div>\n\n<p>data{</p>\n<p>​    box:{box:ture},</p>\n<p>​    box2:{box2:ture},</p>\n<p>​    box3:{box3.true}</p>\n<p>}</p>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p><em>style</em> 的对象语法</p>\n<ul>\n<li><blockquote>\n<p><code>&lt;div :style=&quot;{background:&#39;red&#39;,height:&#39;100px&#39;,height:&#39;100px&#39;}&quot;</code></p>\n</blockquote>\n</li>\n</ul>\n</li>\n<li><p><em>style</em> 的数组语法同<em>class</em> 的数组语法</p>\n<ul>\n<li><blockquote>\n<p>此处不枚举</p>\n</blockquote>\n</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"vue-js-\">Vue.js 的数组方法</h3>\n<ul>\n<li><p>变异的数组方法，具有响应式（已经不是原生的啦）</p>\n<ul>\n<li><code>push()</code></li>\n<li><code>unshift()</code></li>\n<li><code>pop()</code></li>\n<li><code>shift()</code></li>\n<li><code>splice()</code></li>\n<li><code>reverse()</code></li>\n<li><code>sort()</code></li>\n</ul>\n</li>\n<li><p>其他的数组方法均没有响应式。可以通过查看构造函数是否带有_<em>Observer_</em>进行判断</p>\n<ul>\n<li><p>可以通过重新赋值，来实现没有响应式的数组方法，进行响应式</p>\n</li>\n<li><p>复习数组的filter(),map(),find(),findIndex()方法</p>\n<ul>\n<li><pre><code class=\"language-javascript\">filter(callback)//过滤数组，值为真留下来，值为假过滤掉。不能改变原数组，重新生成数组\nvar arr = [1,2,3,4,5,6]\narr = arr.filter((val)=&gt;{\n    return val%2\n})\n//则得到的数组是我们过滤掉偶数的新数组</code></pre>\n</li>\n<li><pre><code class=\"language-javascript\">map(callback)//重新映射数组，对数组的每一项进行重新的返回。不能改变原数组，重新生成数组\nvar arr = [1,2,3,4,5,6]\narr = arr.map((val)=&gt;{\n    return val*val\n})\n//得到一个新数组，每个值为旧数组的每个值得平方</code></pre>\n</li>\n<li><pre><code class=\"language-javascript\">find(callback)//返回数组中，符合条件的第一个元素</code></pre>\n</li>\n<li><pre><code class=\"language-javascript\">findIndex(callback)//返回数组中，符合条件的第一个元素的索引</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n";


