module.exports = "<h1 id=\"vue-js-three-days\">Vue.js Three Days</h1>\n<ul>\n<li>扩展<ul>\n<li>axios发送ajax</li>\n<li>fetch发送ajax</li>\n<li>xhr.status 状态<ul>\n<li>100 准备阶段</li>\n<li>200成功</li>\n<li>300重定向或缓存</li>\n<li>400无法访问与拒绝访问</li>\n<li>500服务器错误</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js 组件</h2>\n<h3 id=\"-\">父子组件的通信</h3>\n<ul>\n<li><p>父组件中的数据，子组件中访问不到。（组件之间是解耦关系）</p>\n<ul>\n<li><p>通过props属性，由子组件向父组件请求数据（父组件向子组件传输数据）</p>\n<ul>\n<li><p>props 可以是数组或对象，用于接收来自父组件的数据。props 可以是简单的数组，或者使用对象作为替代，对象允许配置高级选项，如类型检测、自定义校验和设置默认值。 </p>\n<ul>\n<li><blockquote>\n<p>{</p>\n<p>​    title:{</p>\n<p>​        tyle:String,</p>\n<p>​        defaulte:false</p>\n<p>​    }</p>\n<p>}</p>\n</blockquote>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><p>通过父组件获取的数据，子组件不能修改，这是单项数据流动。</p>\n</li>\n</ul>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;my-header :child-message=&quot;message&quot;&gt;&lt;/my-header&gt;\n&lt;/div&gt;\n&lt;script&gt;\n    //html属性不要出现驼峰写法，HTML5本身不区分大小写,建议使用-代替驼峰写法\n    //但是js环境中不支持-，所以要用驼峰写法。两者相等\n    //通过把父组件的数据挂载到子组件的标签中，子组件在通过props属性取到这个标签上的属性名，就得到了对应的键值对\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        data:{\n            message:&#39;我是来自父组件的问候&#39;\n        },\n        components:{\n            &#39;my-header&#39;:{\n                template:`&lt;div&gt;我是子组件，{{childMessage}}&lt;/div&gt;`,\n                props:[&#39;child-message&#39;]\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n<li><p>同样子组件的数据，父组件也访问不到</p>\n<ul>\n<li>通过自定义事件emit events触发来获取数据</li>\n<li>通过子组件获取的数据，父组件不能修改，这是单项数据流动。</li>\n</ul>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    我是父组件：{{message}}\n    &lt;my-header @custom-child-events=&quot;getChildInfo&quot;&gt;&lt;/my-header&gt;\n&lt;/div&gt;\n&lt;script&gt;\n    //html属性不要出现驼峰写法，HTML5本身不区分大小写,建议使用-代替驼峰写法\n    //但是js环境中不支持-，所以要用驼峰写法。两者相等\n\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        data:{\n            message:&#39;&#39;\n        },\n        methods:{\n            getChildInfo(res){\n                this.message = res\n            }\n        }\n        components:{\n            &#39;my-header&#39;:{\n                template:`&lt;div @click=&quot;shootInfo&quot;&gt;我是子组件:&lt;/div&gt;`,\n                data(){\n                    return {\n                       childMessage:&#39;来自子组件的问候&#39; \n                    }\n                },\n                 mothods:{\n                     shootInfo(){\n                            this.$emit(&#39;custom-child-events&#39;,this.childMessage)\n                             }\n                 }\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n<li><p>非父子组件通信</p>\n<ul>\n<li><p>祖孙通信和兄弟通信，等非父子通信一般用vuex来解决（简单）</p>\n</li>\n<li><p>祖孙组件通信</p>\n<ul>\n<li>先父子通信，在父子通信，一层层的传递。（麻烦）</li>\n</ul>\n</li>\n<li><p>非父子组件通过空实例通信</p>\n<ul>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;my-header&gt;&lt;/my-header&gt;\n    &lt;my-list&gt;&lt;/my-list&gt;\n&lt;/div&gt;\n\n&lt;script&gt;\n    var bus = new Vue()//bus总线（空对象）\n    //bus.$on()绑定事件\n    //bus.$emit()触发事件\n    //为什么不用vm，而是用一个空对象，因为在vm创建的过程中首先就是绑定的事件，所以在参数列表中不能直接用vm引用的事件，是绑定不到vm上面的。\n\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        components:{\n            &#39;my-header&#39;:{\n                template:&#39;&lt;div&gt;{{message}}&lt;/div&gt;&#39;,\n                data(){\n                    return{\n                        message:&#39;&#39;\n                    }\n                },\n                mounted(){\n                    //在生命周期中绑定事件\n                    bus.$on(&#39;data-custom-events&#39;,(payload)=&gt;{\n                        this.message = payload\n                    })\n                }\n            },\n            &#39;my-list&#39;:{\n                template:&#39;&lt;div @click=&quot;handleChange&quot;&gt;{{info}}&lt;/div&gt;&#39;,\n                data(){\n                    return{\n                        info:&#39;非父子通信&#39;\n                    }\n                },\n                methods:{\n                    handleChange(evt){\n                        //触发点击事件时触发自定义事件，发送数据\n                        bus.$emit(&#39;data-custom-events&#39;,evt.target.innerHTML)\n                    }\n                }\n            }\n        }\n    })\n\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"vue-js-\">Vue.js 组件的内容分发</h3>\n<ul>\n<li><p>内容分发：把组件标签内的内容，添加到指定的位置。</p>\n<ul>\n<li><p>默认组件标签内的内容不会被浏览器编译</p>\n</li>\n<li><p>利用插槽标签*<slot></slot>*</p>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;my-header&gt;\n        &lt;span slot=&quot;icon1&quot;&gt;插槽1&lt;/span&gt;\n        &lt;span slot=&quot;icon2&quot;&gt;插槽2&lt;/span&gt;\n    &lt;/my-header&gt;\n&lt;/div&gt;\n\n&lt;script&gt;\n    //通过给组件内的标签添加slot属性指定名字，在template的&lt;slot&gt;标签中添加name\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        components:{\n            &#39;my-header&#39;:{\n                template:&#39;&lt;div&gt;&lt;slot name=&quot;icon1&quot;&gt;我是插槽的默认值，如果找不到name=&quot;icon1&quot;的属性，我就会显示出来&lt;/slot&gt;{{message}}，&lt;solt name=&quot;icon2&quot;&gt;&lt;/solt&gt;&lt;/div&gt;&#39;,\n                data(){\n                    return{\n                        message:&#39;组件分发&#39;\n                    }\n                }\n            })</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n<h3 id=\"-\">动态组件</h3>\n<ul>\n<li><p>vue中有许多自定义属性：<code>is</code> <code>key</code> <code>ref</code></p>\n</li>\n<li><p>vue中有许多自定义标签：<code>&lt;template&gt; &lt;transition&gt; &lt;component&gt;</code></p>\n</li>\n<li><p>所以我们可以通过<code>&lt;component&gt;</code>标签和<code>is</code>属性来实现动态标签</p>\n</li>\n<li><p>组件切换的时候，会全部摧毁重建（不会像标签一样进行复用）</p>\n<ul>\n<li>所以性能会受到影响</li>\n<li>用<code>&lt;keep-alive&gt;</code>标签解决这个问题：保存第一次渲染的组件到内存中，下一次去内存中读取组件（像vue对相同标签进行复用）。所以不会再触发组件的生命周期<ul>\n<li>但是会自动给组件添加两个其他的生命周期，针对缓存的生命周期<ul>\n<li><code>activated</code>在激活<code>&lt;keep-alive&gt;</code>时触发</li>\n<li><code>deactivated</code>在销毁<code>&lt;keep-alive&gt;</code>时触发</li>\n</ul>\n</li>\n<li>同样我们还可以给<code>&lt;keep-alive&gt;</code>标签一个<code>include</code>或者<code>exclude</code>属性来控制是否给某个组件缓存</li>\n</ul>\n</li>\n</ul>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n\n        &lt;!-- &lt;tab1&gt;&lt;/tab1&gt;\n        &lt;tab2&gt;&lt;/tab2&gt;\n        &lt;tab3&gt;&lt;/tab3&gt; --&gt;\n\n        &lt;button @click=&quot;handleChange(1)&quot;&gt;TAB1&lt;/button&gt;\n        &lt;button @click=&quot;handleChange(2)&quot;&gt;TAB2&lt;/button&gt;\n        &lt;button @click=&quot;handleChange(3)&quot;&gt;TAB3&lt;/button&gt;\n\n        &lt;keep-alive&gt;\n            &lt;component :is=&quot;nowComponent&quot;&gt;&lt;/component&gt;\n        &lt;/keep-alive&gt;\n    &lt;/div&gt;\n\n    &lt;script&gt;\n\n    //1.动态组件：&lt;component&gt; 和 is属性(选择指定的组件) 配合使用的\n\n    //2.vue中有很多自定义标签：&lt;component&gt;、&lt;template&gt; 、&lt;transtion&gt; ...\n\n    //3.vue中有很多自定义属性：key属性、ref属性、is属性  ...\n\n    // 4. 组件切换的时候，会全部重新创建。\n    //     1. 性能会有一些小影响\n    //     2. 之前的内容不会保留，例如输入框里的内容。\n        //解决：&lt;keep-alive&gt; : 保留第一次渲染的组件到内存中，下一次去内存中读出这个缓存的组件。\n\n    var vm = new Vue({\n        el : &#39;#app&#39;,\n        data : {\n            nowComponent : &#39;tab1&#39;\n        },\n        components : {\n            &#39;tab1&#39; : {\n                template : &#39;&lt;div&gt;tab1:&lt;input type=&quot;text&quot;&gt;&lt;/div&gt;&#39;\n            },\n            &#39;tab2&#39; : {\n                template : &#39;&lt;div&gt;tab2:&lt;input type=&quot;text&quot;&gt;&lt;/div&gt;&#39;\n            },\n            &#39;tab3&#39; : {\n                template : &#39;&lt;div&gt;tab3:&lt;input type=&quot;text&quot;&gt;&lt;/div&gt;&#39;\n            }\n        },\n        methods : {\n            handleChange(n){\n                this.nowComponent = &#39;tab&#39; + n;\n            }\n        }\n    });    \n\n    &lt;/script&gt;</code></pre>\n</li>\n</ul>\n<h2 id=\"vue-ref-\">Vue 的<code>$ref</code>属性</h2>\n<ul>\n<li><p><code>$ref</code> 对DOM元素进行引用</p>\n<ul>\n<li><p>尽量少使用，vue是数据驱动，尽量避免DOM操作</p>\n</li>\n<li><pre><code class=\"language-html\">&lt;div id=&quot;app&quot;&gt;\n    &lt;input ref=&#39;val&#39; type=&quot;text&quot; /&gt;\n    &lt;button @click=&quot;getInfo&quot;&gt;\n        获取数据\n    &lt;/button&gt;\n&lt;/div&gt;\n&lt;script&gt;\n    var vm = new Vue({\n        el:&#39;#app&#39;,\n        methods:{\n            getInfo(){\n                console.log(this.$refs.val.value)//取的输入框的值\n            }\n        }\n    })\n&lt;/script&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n<h2 id=\"vue-js-\">Vue.js 过度与动画</h2>\n<ul>\n<li><p>专门针对显示与隐藏操作的</p>\n</li>\n<li><p>通过<transition>标签添加过渡与动画</p>\n<ul>\n<li><p>图示</p>\n</li>\n<li><p><img src=\"" + require("./.\\transition.png") + "\" alt=\"\"></p>\n</li>\n<li><p>注意  *<em>v *</em>  是一个变量，就是<transition name=\"v\">  </p>\n</li>\n<li><p><code>v-enter</code>  第0帧</p>\n</li>\n<li><p><code>v-enter-to</code>  第1帧-------最后1帧（动画消失）</p>\n</li>\n<li><p><code>v-enter-active</code>  动画全程都存在，所以这个属性上面可以写css的<code>animate</code></p>\n</li>\n<li><p><code>v-leave</code>  第0帧</p>\n</li>\n<li><p><code>v-leave-to</code>  第1帧-------最后1帧（动画消失）</p>\n</li>\n<li><p><code>v-leave-active</code>  动画全程都存在，所以这个属性上面可以写css的<code>animate</code></p>\n</li>\n<li><pre><code class=\"language-html\">&lt;style&gt;\n    v-enter:{opcity:0};\n    v-enter-to:{opcity:1};//可以省略\n    v-enter-active:{transition:1s}//因为它全程都在，所以给它加过渡属性\n    v-leave:{opcity:1};\n    v-leave-to:{opcity:0};//可以省略\n    v-leave-active:{transition:1s}//因为它全程都在，所以给它加过渡属性\n&lt;/style&gt;\n&lt;transition name=&quot;v&quot;&gt;\n    &lt;div&gt;\n        我有背景色\n    &lt;/div&gt;\n&lt;/transition&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p><strong>animate.css</strong>的使用</p>\n<ul>\n<li><p>一个专门做c3动画的库</p>\n</li>\n<li><p>引用css文件，在你想添加动画效果的元素上面添加 <code>class=&quot;animated xxxx infinite&quot;</code> 等<code>infinite</code>是选择动画是否无限</p>\n</li>\n<li><p>我们可以通过定义<transition>中的属性使用这个库</p>\n</li>\n<li><pre><code class=\"language-html\">&lt;link rel=&quot;stylesheet&quot; href=&quot;./animate.css&quot;&gt;\n&lt;transition name=&quot;v&quot; leave-action-class=&quot;xxxx&quot; enter-action-class=&quot;xxxx&quot;&gt;\n    &lt;div class=&quot;animated&quot;&gt;\n        必须有animated\n    &lt;/div&gt;\n&lt;/transition&gt;</code></pre>\n</li>\n</ul>\n</li>\n<li><p><code>appear appear-active-class</code>在<transition>标签上添加这句话可以自动执行动画，不需要触发条件</p>\n</li>\n<li><p>多元素切换的时候，最好通过设置不同的<code>key</code>属性来区分相同标签名的元素，以避免vue的复用</p>\n</li>\n<li><p>我们同样可以通过在<transition>标签上添加属性<code>mode=&quot;in-out&quot;/mode=&quot;out-in&quot;</code>来控制先进行<em>enter</em>还是<em>leave</em></p>\n</li>\n<li><p>列表过渡（必须设置key属性）</p>\n<ul>\n<li><p>使用<transition-group>标签包裹列表标签</p>\n</li>\n<li><p><transition-group>默认会自动渲染到页面变成一个<span>标签，<transition>不会影响页面</p>\n</li>\n<li><p>我们可以通过给<transition-group>标签添加<code>tag</code>  属性来指定它变成的标签名</p>\n</li>\n<li><pre><code class=\"language-html\">&lt;!DOCTYPE html&gt;\n&lt;html lang=&quot;en&quot;&gt;\n&lt;head&gt;\n    &lt;meta charset=&quot;UTF-8&quot;&gt;\n    &lt;title&gt;Document&lt;/title&gt;\n    &lt;style&gt;\n    .slide-enter-active{ animation:.5s move linear; }\n    .slide-leave-active{ animation:.5s move linear reverse;}\n    @keyframes move{\n        0%{ transform : translateX(100px); opacity:0;}\n        100%{ transform : translateX(0); opacity:1;}\n    }\n    &lt;/style&gt;\n    &lt;script src=&quot;vue.js&quot;&gt;&lt;/script&gt;\n&lt;/head&gt;\n&lt;body&gt;\n    &lt;div id=&quot;app&quot;&gt;\n\n        &lt;input type=&quot;text&quot; v-model=&quot;message&quot;&gt;&lt;button @click=&quot;handleAddMessage&quot;&gt;留言&lt;/button&gt;\n\n        &lt;transition-group tag=&quot;ul&quot; name=&quot;slide&quot;&gt; \n            &lt;li v-for=&quot;item,index in list&quot; :key=&quot;item.id&quot;&gt;{{ item.content }}  &lt;button @click=&quot;handleRemoveMessage(index)&quot;&gt;X&lt;/button&gt;&lt;/li&gt;\n        &lt;/transition-group&gt;\n\n        &lt;div&gt;总计：{{ list.length }}&lt;/div&gt;\n    &lt;/div&gt;\n    &lt;script&gt;\n\n    //1. &lt;transition-group&gt; : 默认会渲染成一个&lt;span&gt;标签\n    //2. tag属性：指定要渲染成的标签\n    var vm = new Vue({\n        el : &#39;#app&#39;,\n        data : {\n            isShow : true,\n            /*list : [&#39;默认的留言1&#39; , &#39;默认的留言2&#39;],*/\n            list : [\n                { &quot;id&quot; : 1 , &quot;content&quot; : &quot;默认的留言1&quot; },\n                { &quot;id&quot; : 2 , &quot;content&quot; : &quot;默认的留言2&quot; },\n                { &quot;id&quot; : 3 , &quot;content&quot; : &quot;默认的留言3&quot; }\n            ],\n            message : &#39;&#39;\n        },\n        methods : {\n            handleAddMessage(){\n                //this.list.unshift(this.message);\n                this.list.unshift({\n                    id : this.list.length + 1,\n                    content : this.message\n                });\n                this.message = &#39;&#39;;\n            },\n            handleRemoveMessage(index){\n                this.list.splice(index,1);\n            }\n        }\n    });\n\n    &lt;/script&gt;\n&lt;/body&gt;\n&lt;/html&gt;</code></pre>\n</li>\n</ul>\n</li>\n</ul>\n";


