<template>
  <div id="snake" @click="handleShow" @keydown="handleMove" tabindex="1">
    <div class="score">
      <p>计分板</p>
      <span ref="score">0</span>
    </div>
    <canvas width="500" height="500" ref="cans" ></canvas>
    <audio src="/static/music.mp3" loop ref="music">您的浏览器不支持</audio>
  </div>
</template>

<script>
  export default {
    name: "snake",
    data(){
      return{
        show:true,
        id:'',
        createSnake:null,
        snakeBody:[{x:0,y:0},{x:10,y:0},{x:20,y:0},{x:30,y:0},{x:40,y:0}],
        moveSnake:null,
        directX:0,
        directY:1,
        createFood:null,
        clearSnake:null,
        foodX:Math.floor(Math.random()*500),
        foodY:Math.floor(Math.random()*500),
        eatFood:null,
        dieSnake:null,
        score:null
      }
    },
    mounted(){
      var ctx = this.$refs.cans.getContext('2d');

      //初始化蛇身子
      this.createSnake = function () {
        ctx.clearRect(0,0,this.$refs.cans.width,this.$refs.cans.height);
        for(var i = 0 ; i < this.snakeBody.length ; i++){
          ctx.save()
          ctx.beginPath();
          // todo 解决丢失一个方块问题（实际重叠了）
          // console.log(this.snakeBody[i].x)
          ctx.fillRect(this.snakeBody[i].x,this.snakeBody[i].y,10,10);
          ctx.closePath();
          ctx.restore();
        }
      }

      //移动蛇
      this.moveSnake = function () {
        ctx.clearRect(0,0,this.$refs.cans.width,this.$refs.cans.height);
        this.snakeBody[0].x+= 10*this.directX;
        this.snakeBody[0].y+= 10*this.directY;

        /*var tempX ;
        var tempY ;
        var preX = this.snakeBody[0].x;
        var preY = this.snakeBody[0].y;
        for(var i = 1 ; i < this.snakeBody.length ; i++){
          //todo 注意引用类型！
          tempX = this.snakeBody[i].x;
          tempY = this.snakeBody[i].y;
          this.snakeBody[i].x = preX;
          this.snakeBody[i].y = preY;
          preX = tempX;
          preY = tempY;
        }*/

        for(var i=this.snakeBody.length-1; i>0; i--) {
          //todo 注意引用类型！
          this.snakeBody[i].x = this.snakeBody[i-1].x;
          this.snakeBody[i].y = this.snakeBody[i-1].y;
        }
        this.createSnake();
        this.createFood();
        this.eatFood();
        this.dieSnake();
      }

      //制造食物
      this.createFood = function () {
        ctx.save()
        ctx.beginPath();
        ctx.fillStyle = 'red'
        ctx.fillRect(this.foodX,this.foodY,10,10)
        ctx.closePath();
        ctx.restore()
      }

      //吃到食物
      this.eatFood = function () {
        var x = this.snakeBody[0].x;
        var y = this.snakeBody[0].y;
        if(this.foodY<y+10 && this.foodY+10 > y && this.foodX<x+10 && this.foodX+10>x){
          this.foodX = Math.floor(Math.random()*500);
          this.foodY = Math.floor(Math.random()*500);
          var last = this.snakeBody[this.snakeBody.length - 1];
          var plast = this.snakeBody[this.snakeBody.length - 2];
          var px = last.x - plast.x;
          var py = last.y - plast.y;
          var newEle = {x : last.x + px , y : last.y + py}; //创建一个新身体
          this.snakeBody.push(newEle);
          this.score();
        }
      }
      //判断蛇是否死亡
      this.dieSnake = function () {
        if(this.snakeBody[0].x > this.$refs.cans.width || this.snakeBody[0].x < 0 || this.snakeBody[0].y > this.$refs.cans.height || this.snakeBody[0].y < 0) {
          clearInterval(this.id);
          /*
          有bug 不好用
          document.styleSheets[0].addRule('#snake::after','display: block');
          window.getComputedStyle(this.$refs.cans, '::after').getPropertyValue('content');
          document.styleSheets[0].addRule('#snake::after','content: " game over "');
          document.styleSheets[0].insertRule('#snake::after { content: " game over " }', 0);
          */
          alert('is Died')
        }
      }
      //计数方法
      this.score = function () {
        this.$refs.score.innerHTML++
      }


    },
    methods:{
      handleMove(evt){
        // 上
        if(evt.keyCode === 87){
          this.directX = 0;
          this.directY = -1;
        }
        // 下
        if(evt.keyCode === 83){
          this.directX = 0;
          this.directY = 1;
        }
        // 左
        if(evt.keyCode === 65){
          this.directX = -1;
          this.directY = 0;
        }
        // 右
        if(evt.keyCode === 68){
          this.directX = 1;
          this.directY = 0;
        }
      },
      handleShow(evt){
        if(this.show){
          this.$refs.music.play();
          this.createSnake();
          this.createFood();
          this.show = false;
          this.id = setInterval(()=>{this.moveSnake()},200)
        }
        document.styleSheets[0].addRule('#snake::after','display: none');
      }
    }
  }
</script>

<style scoped lang="scss">
  #snake{
    position: relative;
    &::after{
      content: '单机开始游戏';
      width:100%;
      height: 100%;
      background: black;
      text-align: center;
      line-height: 500px;
      color:white;
      opacity: 0.5;
      position: absolute;
      left: 0;
      top: 0;
      font-size: 28px;
      font-weight: bold;
    }
    canvas{
      display: block;
      margin:0 auto;
      background:grey;
      outline: none;
    }
    .score{
      width:100px;
      height: 50px;
      background: white;
      position: fixed;
      left: 10px;
      top: 10px;
      p{
        width: 100%;
        height:30px;
        font-size: 18px;
        line-height: 30px;
        text-align: center;
      }
      span{
        text-align: center;
        color: #123;
        font-size: 16px;
        line-height: 20px;
        width:100%;
        height: 20px;
        display: block;
      }
    }
  }

</style>




